import { valueTypeDefinitions, ValueSubTypeDefinition } from './value-type-definition';
import { SettingKey, ISettingsReader } from '../settings-reader';
import { ValueType, ValueSubType } from './value-types';

export interface IValueConverter {
    getUnit(valueType: ValueType): string;
    getConvertedValue(rawValue: any, valueType: ValueType): number | string;
}
export class ValueConverter implements IValueConverter {

    private _selectedConversions: Map<ValueType, ValueSubTypeDefinition> = new Map([]);
    private _settingsReader: ISettingsReader;
    constructor(settingsReader: ISettingsReader) {
        this._settingsReader = settingsReader;
        this._settingsReader.addOnSettingsReloadedDelegate(() => this._propagateSettings());
        if (this._settingsReader.isLoaded) {
            this._propagateSettings();
        }
    }

    public getUnit(valueType: ValueType): string {
        if (valueType == null) {
            return '';
        }

        let subDefinition = this._selectedConversions.get(valueType);
        if (subDefinition == null) {
            this._setDefaultConversion(valueType);
            subDefinition = this._selectedConversions.get(valueType);
        }
        return subDefinition.unitLabel;
    }

    public getConvertedValue(rawValue: any, valueType: ValueType): number | string {
        if (valueType == null) {
            return rawValue;
        }
        let subDefinition = this._selectedConversions.get(valueType);
        if (subDefinition == null) {
            this._setDefaultConversion(valueType);
            subDefinition = this._selectedConversions.get(valueType);
        }

        return subDefinition.conversion(rawValue);
    }

    private _setDefaultConversion(valueTypeId: ValueType): void {
        const valueType = valueTypeDefinitions.find((x) => x.valueType === valueTypeId);
        let valueSubType = ValueSubType.DEFAULT;
        let subTypeDefinition: ValueSubTypeDefinition = null;
        if (valueType != null && valueType.defaultValueSubType != null) {
            valueSubType = valueType.defaultValueSubType;
            subTypeDefinition = valueType.valueSubTypes.find((x) => x.valueSubType === valueSubType);
        }
        if (subTypeDefinition == null) {
            subTypeDefinition = this._createNoConversionSubTypeDefinition(valueSubType);
        }
        this._selectedConversions.set(valueTypeId, subTypeDefinition);
    }

    private _createNoConversionSubTypeDefinition(subValueType: ValueSubType): ValueSubTypeDefinition {
        const subTypeDefinition = new ValueSubTypeDefinition();
        subTypeDefinition.unitLabel = '';
        subTypeDefinition.conversion = (value) => value;
        subTypeDefinition.valueSubType = subValueType;
        return subTypeDefinition;
    }

    private _propagateSettings(): void {
        this._setConversionFromSettingsData(ValueType.DATAITEM_DEPTH, this._settingsReader.getSetting(SettingKey.depth));
        this._setConversionFromSettingsData(ValueType.DATAITEM_DISTANCE_DEC3, this._settingsReader.getSetting(SettingKey.distanceUnit));
        this._setConversionFromSettingsData(ValueType.DATAITEM_FUELECONOMY_DEC3, this._settingsReader.getSetting(SettingKey.fuelEconomyUnit));
        this._setConversionFromSettingsData(ValueType.DATAITEM_HEIGHT_DEC3, this._settingsReader.getSetting(SettingKey.height));
        this._setConversionFromSettingsData(ValueType.DATAITEM_PRESSURE_DEC3, this._settingsReader.getSetting(SettingKey.pressure));
        this._setConversionFromSettingsData(ValueType.DATAITEM_ATMOSPHERIC_PRESSURE_DEC3, this._settingsReader.getSetting(SettingKey.atmosphericPressure));
        this._setConversionFromSettingsData(ValueType.DATAITEM_SPEED_MET_PER_SEC_DEC3, this._settingsReader.getSetting(SettingKey.speed));
        this._setConversionFromSettingsData(ValueType.DATAITEM_TEMPERATURE_KELVIN_DEC3, this._settingsReader.getSetting(SettingKey.temperature));
        this._setConversionFromSettingsData(ValueType.DF38_TEMPERATURE_HIGH, this._settingsReader.getSetting(SettingKey.temperature));
        this._setConversionFromSettingsData(ValueType.DF39_TEMPERATURE_LOW, this._settingsReader.getSetting(SettingKey.temperature));
        this._setConversionFromSettingsData(ValueType.DF105_TEMPERATURE_EXTENDED_RANGE_AND_PRECISION, this._settingsReader.getSetting(SettingKey.temperature));
        this._setConversionFromSettingsData(ValueType.DATAITEM_CUMETRES_DEC3, this._settingsReader.getSetting(SettingKey.volume));
        this._setConversionFromSettingsData(ValueType.DF18_FLOW_RATE_LOW, this._settingsReader.getSetting(SettingKey.volumeRate));
        this._setConversionFromSettingsData(ValueType.DATAITEM_VOLUMERATE_CUMS_DEC3, this._settingsReader.getSetting(SettingKey.volumeRate));
        this._setConversionFromSettingsData(ValueType.DF44_VOLUME, this._settingsReader.getSetting(SettingKey.volume));
        this._setConversionFromSettingsData(ValueType.DF47_PRESSURE_MEDIUM, this._settingsReader.getSetting(SettingKey.pressure));
        this._setConversionFromSettingsData(ValueType.DF09_DISTANCE, this._settingsReader.getSetting(SettingKey.distanceUnit));
        this._setConversionFromSettingsData(ValueType.DF109_DISTANCE_ROUGH, this._settingsReader.getSetting(SettingKey.depth));
        this._setConversionFromSettingsData(ValueType.DF35_SPEED_OVER_GROUND, this._settingsReader.getSetting(SettingKey.speed));
        this._setConversionFromSettingsData(ValueType.DF46_DISTANCE_SIGNED_MEDIUM, this._settingsReader.getSetting(SettingKey.depth));
        this._setConversionFromSettingsData(ValueType.DF29_PRESSURE,this._settingsReader.getSetting(SettingKey.pressure));
    }

    private _setConversionFromSettingsData(valueTypeId: ValueType, settingId: string): void {
        const valueType = valueTypeDefinitions.find((x) => x.valueType === valueTypeId);
        if (valueType != null) {
            const subType = valueType.valueSubTypes.find((x) => x.wduSettingsId === settingId);
            if (subType != null) {
                this._selectedConversions.set(valueTypeId, subType);
            }
        }
    }

}
