export class ApplicationSettings {
    public index: number;
    public type: ApplicationType;
    public hasAppSelector: boolean;

    public static readApplicationSettings(): ApplicationSettings {
        const jsonString = document.getElementById('application-settings').innerHTML;
        const applicationSettings = JSON.parse(jsonString) as ApplicationSettings;
        return applicationSettings;
    }
}

// eslint-disable-next-line no-shadow
export enum ApplicationType {
    normal = 0,
    overlaybar = 1
}
