import { ICommunicationHandler } from '../../infrastructure/communication/communication-handler';
import { MfdStatusUpdateDataContent, StatusUpdateDataContent } from '../../infrastructure/communication/protocol';
import { CommunicationElement } from './communication-element';
import { Component } from './components/component';
import { Visibility } from './components/visibility';
import { HtmlHelpers, NumberHelpers } from './helpers';
import { Page } from './page';

export class Layer extends CommunicationElement {
    // eslint-disable-next-line @typescript-eslint/no-empty-function
    public onUnavailableChanged(): void { }
    public allowSignalTypes(): number[] { return [] }
    // eslint-disable-next-line @typescript-eslint/no-empty-function
    public evaluateVisibility(): void { }

    private _id: string;
    private _visible: boolean;
    private _components: Component[];
    private _page: Page;
    private _isUnavailable: boolean = false;
    private _isVisible: boolean = false;
    private _element: HTMLElement;

    constructor(element: HTMLElement, communicationHandler: ICommunicationHandler, page: Page) {
        super(communicationHandler, element);
        this._element = element;
        this._page = page;
        this._id = element.id;
        this._visible = NumberHelpers.parseBooleanArgument(element.dataset.showInitially);
    }

    public get id(): string {
        return this._id;
    }

    public get visible(): boolean {
        return this._visible;
    }

    public set visible(value: boolean) {
        this._visible = value;
        this._evaluateVisibility();
        if (this._isUnavailable) {
            return;
        }

        const componentVisibility = this._visible && this._page.isVisible;
        for (const component of this._components) {
            component.setVisibility(componentVisibility ? Visibility.visible : Visibility.hidden);
        }
    }

    public onMfdDataReceived(message: MfdStatusUpdateDataContent): void {
        if (this._isUnavailable !== message.unavailable) {
            this._isUnavailable = message.unavailable;
            this.visible = !this._isUnavailable;
        }
    }

    public onNmeaDataReceived(message: StatusUpdateDataContent): void {
        if (this._isUnavailable !== message.unavailable) {
            this._isUnavailable = message.unavailable;
            this.visible = !this._isUnavailable;
        }
    }

    public initialize(components: Component[]): void {
        this._components = components || [];
        this.visible = this._visible; // this triggers the visibility evaluation
    }

    public onParentVisibilityChanged(): void {
        const visible = this._visible && this._page.isVisible;
        for (const component of this._components) {
            component.setVisibility(visible ? Visibility.visible : Visibility.hidden);
        }
    }

    public redraw(): void {
        if (this._visible) {
            for (const component of this._components) {
                component.refresh();
            }
        }
    }

    private _evaluateVisibility(): void {
        const visible = this.visible && !this._isUnavailable;
        if (visible !== this._isVisible) {
            this._isVisible = visible;
            HtmlHelpers.setElementVisibility(this._element, visible);
        }
    }
}
