export class HtmlHelpers {
    public static hideElement(element: HTMLElement): void {
        if (element != null) {
            element.classList.add('hidden');
        }
    }

    public static showElement(element: HTMLElement): void {
        if (element != null) {
            element.classList.remove('hidden');
        }
    }

    public static setElementVisibility(element: HTMLElement, visible: boolean): void {
        if (element != null) {
            if (visible === true) {
                this.showElement(element);
            } else {
                this.hideElement(element);
            }
        }
    }

    public static debounce(func: () => void, wait: number, immediate: boolean): () => void {
        let timeout: any;
        return () => {
            // TODO: Get rid of these warnings
            // eslint-disable-next-line @typescript-eslint/no-this-alias
            const context = this;
            // eslint-disable-next-line prefer-rest-params
            const args = arguments;
            const later = () => {
                timeout = null;
                if (!immediate) {
                    func.apply(context, args);
                }
            };
            const callNow = immediate && !timeout;
            clearTimeout(timeout);
            timeout = setTimeout(later, wait);
            if (callNow) {
                func.apply(context, args);
            }
        };
    }

}

export class NumberHelpers {

    public static round(number: number, precision: number): number {
        const shift = (numberArg: number, precisionArg: number, reverseShiftArg: boolean) => {
            if (reverseShiftArg) {
                precisionArg = -precisionArg;
            }
            const numArray = ('' + numberArg).split('e');
            return +(numArray[0] + 'e' + (numArray[1] ? (+numArray[1] + precisionArg) : precisionArg));
        };
        return shift(Math.round(shift(number, precision, false)), precision, true);
    }

    public static parseBooleanArgument(arg: any): boolean {
        if (arg === undefined) {
            return false;
        }
        else if (typeof (arg) === 'boolean') {
            return arg;
        }
        else if (typeof (arg) === 'string') {
            return arg.toLowerCase() === 'true';
        }
        else {
            return false;
        }
    }

    public static clamp(value: number, min: number, max: number): number {
        if (value > max) {
            return max;
        }
        if (value < min) {
            return min;
        }
        return value;
    }
}
