import { HtmlHelpers, NumberHelpers } from '../helpers';
import { MfdStatusType, MfdStatusUpdateDataContent, Protocol } from '../../../infrastructure/communication/protocol';
import { ICommunicationHandler } from '../../../infrastructure/communication/communication-handler';
import { Component } from './component';

export class Button extends Component {
    private _isMomentaryButton: boolean;
    private _stackImages: boolean;
    private _error1: boolean = false;
    private _error2: boolean = false;
    private _on: boolean = false;
    private _pressed: boolean = false;
    private _imgOff: HTMLElement;
    private _imgOn: HTMLElement;
    private _imgError1: HTMLElement;
    private _imgError2: HTMLElement;
    private _imgError12: HTMLElement;
    private _imgNotAvailable: HTMLElement;
    private _imgPressed: HTMLElement;
    private _globalMouseUpEvent: (event: MouseEvent) => boolean;

    constructor(element: HTMLElement, communicationHandler: ICommunicationHandler) {
        super(element, communicationHandler);
    }

    public onRedraw(): void {
        if (this._stackImages) {
            HtmlHelpers.showElement(this._imgOff);
            HtmlHelpers.setElementVisibility(this._imgOn, this._on);
            HtmlHelpers.setElementVisibility(this._imgError12, this._error1 && this._error2);
            HtmlHelpers.setElementVisibility(this._imgError1, this._error1);
            HtmlHelpers.setElementVisibility(this._imgError2, this._error2);
            HtmlHelpers.setElementVisibility(this._imgNotAvailable, this.isUnavailable);
        }
        else {
            // Do not stack images; show one image at a time instead.
            HtmlHelpers.hideElement(this._imgOff);
            HtmlHelpers.hideElement(this._imgOn);
            HtmlHelpers.hideElement(this._imgError1);
            HtmlHelpers.hideElement(this._imgError2);
            HtmlHelpers.hideElement(this._imgError12);
            HtmlHelpers.hideElement(this._imgNotAvailable);

            if (this.isUnavailable) {
                HtmlHelpers.showElement(this._imgNotAvailable);
            } else if (this._error1 && this._error2) {
                HtmlHelpers.showElement(this._imgError12);
            } else if (this._error1) {
                HtmlHelpers.showElement(this._imgError1);
            } else if (this._error2) {
                HtmlHelpers.showElement(this._imgError2);
            } else if (this._on) {
                HtmlHelpers.showElement(this._imgOn);
            } else {
                HtmlHelpers.showElement(this._imgOff);
            }
        }

        if (this._pressed && !this.isUnavailable) {
            HtmlHelpers.showElement(this._imgPressed);
        }
        else {
            HtmlHelpers.hideElement(this._imgPressed);
        }
    }

    public allowSignalTypes(): MfdStatusType[] {
        return [MfdStatusType.pulse, MfdStatusType.momentary];
    }
    public onMfdDataReceived(data: MfdStatusUpdateDataContent): void {
        this._error1 = data.err1FT;
        this._error2 = data.err2UC;
        this._on = data.onOffStatus;
        this.refresh();
    }

    public onVisibilityChanged(visible: boolean): void {
        if (this._pressed && !visible) {
            this.buttonUp();
        }
    }

    public onUnavailableChanged(): void {
        if (this._pressed && this.isUnavailable) {
            this.buttonUp();
        }
    }

    protected onInitialize(): void {
        this._isMomentaryButton = this.element.dataset.function === 'SendMomentary';
        this._stackImages = NumberHelpers.parseBooleanArgument(this.element.dataset.stackImages);
        this._imgOff = this.element.querySelector('img.button-off');
        this._imgOn = this.element.querySelector('img.button-on');
        this._imgError1 = this.element.querySelector('img.button-error1');
        this._imgError2 = this.element.querySelector('img.button-error2');
        this._imgError12 = this.element.querySelector('img.button-error12');
        this._imgNotAvailable = this.element.querySelector('img.button-notavailable');
        this._imgPressed = this.element.querySelector('img.button-pressed');

        this._globalMouseUpEvent = (event: MouseEvent) => {
            this.buttonUp();
            event.preventDefault();
            return false;
        };

        this._registerEvents();

    }
    private buttonDown(event: Event): void {
        if (this.isUnavailable) {
            return; // Not allowed to press.
        }

        this._pressed = true;

        if (event.type === 'mousedown') {
            document.addEventListener('mouseup', this._globalMouseUpEvent);
        }

        this.refresh();

        if (this._isMomentaryButton) {
            this.communicationHandler.send(Protocol.momentaryCommand(this.mfdSignalId, true));
        }
        else {
            this.communicationHandler.send(Protocol.toggleCommand(this.mfdSignalId, true, this._on ? false : true, this._on ? true : false));
        }
    }

    private buttonUp(): void {
        document.removeEventListener('mouseup', this._globalMouseUpEvent);

        if (this._isMomentaryButton && this._pressed) {
            this.communicationHandler.send(Protocol.momentaryCommand(this.mfdSignalId, false));
        }

        this._pressed = false;
        this.refresh();
    }

    private _registerEvents(): void {
        ['mousedown', 'touchstart'].forEach(
            (eventType) => this.element.addEventListener(eventType, (event: Event) => {
                this.buttonDown(event);
                event.preventDefault();
                event.stopPropagation();
                return false;
            }));

        ['mouseup', 'touchend', 'touchcancel'].forEach(
            (eventType) => this.element.addEventListener(eventType, (event: Event) => {
                this.buttonUp();
                event.preventDefault();
                return false;
            }));
    }
}
