import { Log } from '../log';

// eslint-disable-next-line no-shadow
export enum SocketState {
    notSet = 0,
    closed = 1,
    connecting = 2,
    open = 3,
    socketError = 4
}

export type OnSocketStateChangedDelegate = (state: SocketState) => void;
export class WebSocketManager {

    // TODO: queue messages
    public currentState: SocketState = SocketState.notSet;
    public autoReconnectOnError: boolean = false
    private _webSocket: WebSocket = null;
    private _onMessageDelegates: ((data: any) => void)[] = [];
    private _onSocketStateChangesDelegates: OnSocketStateChangedDelegate[] = [];

    public connect(): void {
        this._createNewWebSocket();
    }

    public addOnDataReceivedDelegate(delegate: (data: any) => void): void {
        this._onMessageDelegates.push(delegate);
    }

    public removeOnDataReceivedDelegate(delegate: (data: any) => void): void {
        for (let i = 0; i < this._onMessageDelegates.length; i++) {
            if (this._onMessageDelegates[i] === delegate) {
                this._onMessageDelegates.splice(i, 1);
                return;
            }
        }
    }

    public addOnSocketStateChangedDelegate(delegate: OnSocketStateChangedDelegate): void {
        this._onSocketStateChangesDelegates.push(delegate);
        delegate(this.currentState)
    }

    public removeOnSocketStateChangedDelegate(delegate: OnSocketStateChangedDelegate): void {
        for (let i = 0; i < this._onSocketStateChangesDelegates.length; i++) {
            if (this._onSocketStateChangesDelegates[i] === delegate) {
                this._onSocketStateChangesDelegates.splice(i, 1);
                return;
            }
        }
    }
    public sendJson(data: any): void {
        this.send(JSON.stringify(data));
    }
    public send(data: string): void {
        if (this._webSocket == null || this._webSocket.readyState !== this._webSocket.OPEN) {
            Log.logWarning('Websocket is not open, failed to send command!');
        } else {
            Log.logDebug('Sending ws data: ', data);
            this._webSocket.send(data);
        }
    }
    private _createNewWebSocket(): void {
        if (this._webSocket != null) {
            try {
                if (this._webSocket.readyState === this._webSocket.OPEN || this._webSocket.readyState === this._webSocket.CONNECTING) {
                    this._webSocket.close();
                }
                this._onSocketClosed();
            } catch {
                // nop
            }
        }
        // this._webSocket = new WebSocket('ws://localhost/ws');
        // this._webSocket = new WebSocket('ws://172.16.71.235/ws');
        this._webSocket = new WebSocket('ws://' + window.location.host + '/ws');
        // this._webSocket = new WebSocket('ws://192.168.1.100/ws');
        this._webSocket.onopen = (event: Event) => this._onOpen(event);
        this._webSocket.onmessage = (event: MessageEvent) => this._onMessageReceived(event);
        this._webSocket.onerror = (event: Event) => this._onSocketError(event);
        this._webSocket.onclose = (_event: CloseEvent) => this._onSocketClosed();

        this._onSocketStateChanged(SocketState.connecting);
    }

    private _onMessageReceived(event: MessageEvent): void {
        const data = JSON.parse(event.data);
        if (data == null) {
            Log.logDebug('Received ws data with value null');
            return;
        }
        Log.logDebug(`Received ws data. cmd: ${data.messagecmd}, type: ${data.messagetype}, size: ${data.size}`);
        for (const delegate of this._onMessageDelegates) {
            delegate(data);
        }
    }

    private _onSocketError(_event: Event): void {
        Log.logError('Websocket return onError, socket state: ' + this._webSocket.readyState);
        this._onSocketStateChanged(SocketState.socketError);
        if (this.autoReconnectOnError) {
            Log.logWarning('Websocket error: Recreate websocket');
            this._createNewWebSocket();
        }

    }
    private _onSocketClosed(): void {
        this._onSocketStateChanged(SocketState.closed);
    }
    private _onOpen(_event: Event): void {
        this._onSocketStateChanged(SocketState.open);
    }

    private _onSocketStateChanged(state: SocketState): void {
        this.currentState = state;
        this._notifySocketStateSubscribers(state);
    }

    private _notifySocketStateSubscribers(state: SocketState): void {
        for (const delegate of this._onSocketStateChangesDelegates) {
            delegate(state);
        }
    }
}
