/* eslint-disable no-bitwise */
import { CommunicationHandler } from '../../../infrastructure/communication/communication-handler';
import { MfdStatusType, MfdStatusUpdateDataContent } from '../../../infrastructure/communication/protocol';
import { Component } from './component';

export class ColorRectangle extends Component {
    private _bgColorEbpSignalId: number;
    private _opacityEbpSignalId: number;
    private _bgColor: string = '0,0,0';
    private _opacityLevel: number = 0.0;

    constructor(element: HTMLElement, communicationHandler: CommunicationHandler) {
        super(element, communicationHandler);
    }

    public allowSignalTypes(): MfdStatusType[] {
        return [];
    }

    public onRedraw(): void {
        this.element.style.background = this._rgbaValue;
    }
    public get ebpSignalIds(): number[] { return [this._bgColorEbpSignalId, this._opacityEbpSignalId]; }

    public onOpacityDataReceived(data: MfdStatusUpdateDataContent): void {
        this._opacityLevel = data.value / 1000;
        this.refresh();
    }
    public onColorDataReceived(data: MfdStatusUpdateDataContent): void {

        const value = data.value / 100; // signal is multiplied by 100 to fit raymarine so we need to divide by 100.
        const blue = (value >> 0) & 0xFF; // (value & 0xFF)
        const green = (value >> 8) & 0xFF; // (value & 0xFF00) >> 8
        const red = (value >> 16) & 0xFF; // (value & 0xFF0000) >> 16
        this._bgColor = `${red},${green},${blue}`;
        this.refresh();
    }

    protected onInitialize(): void {
        this._bgColorEbpSignalId = parseInt(this.element.dataset.ebpBgcolorSignalId, 10);
        this._opacityEbpSignalId = parseInt(this.element.dataset.ebpOpacitySignalId, 10);
        if (!isNaN(this._bgColorEbpSignalId)) {
            this.communicationHandler.addOnMfdValueReceivedDelegate(this._bgColorEbpSignalId, (value: MfdStatusUpdateDataContent) => this.onColorDataReceived(value));
        }
        if (!isNaN(this._opacityEbpSignalId)) {
            this.communicationHandler.addOnMfdValueReceivedDelegate(this._opacityEbpSignalId, (value: MfdStatusUpdateDataContent) => this.onOpacityDataReceived(value));
        }

    }
    private get _rgbaValue(): string {
        return `rgba(${this._bgColor}, ${this._opacityLevel})`;
    }
}
