import { Component } from './component';
import { HtmlHelpers, NumberHelpers } from '../helpers';
import { ScreenSaver } from '../screen-saver';
import { ICommunicationHandler } from '../../../infrastructure/communication/communication-handler';
import { MfdStatusType } from '../../../infrastructure/communication/protocol';

export class ScreenSaverButton extends Component {
    private _imageEnabled: HTMLElement;
    private _imagePressed: HTMLElement;
    private _imageDisabled: HTMLElement;
    private _stackImages: boolean;

    private _isPressed: boolean = false;
    private _globalMouseUpEvent: (event: MouseEvent) => boolean;

    private _screenSaver: ScreenSaver;

    constructor(element: HTMLElement, communicationHandler: ICommunicationHandler, screenSaver: ScreenSaver) {
        super(element, communicationHandler);
        this._screenSaver = screenSaver;
    }

    public allowSignalTypes(): MfdStatusType[] {
        return [];
    }

    public onRedraw(): void {
        const enabled = this._screenSaver.isEnabled;
        const pressed = enabled && this._isPressed;

        if (this._stackImages) {
            HtmlHelpers.setElementVisibility(this._imageEnabled, true);
            HtmlHelpers.setElementVisibility(this._imagePressed, pressed);
            HtmlHelpers.setElementVisibility(this._imageDisabled, !enabled);
        } else {
            HtmlHelpers.hideElement(this._imageEnabled);
            HtmlHelpers.hideElement(this._imageDisabled);
            HtmlHelpers.hideElement(this._imagePressed);
            if (enabled && !pressed) {
                HtmlHelpers.showElement(this._imageEnabled);
            } else if (enabled && pressed) {
                HtmlHelpers.showElement(this._imagePressed);
            } else {
                HtmlHelpers.showElement(this._imageDisabled);
            }
        }
    }
    protected onInitialize(): void {
        this._imageEnabled = this.element.querySelector('.screensaver-enabled');
        this._imagePressed = this.element.querySelector('.screensaver-pressed');
        this._imageDisabled = this.element.querySelector('.screensaver-disabled');
        this._stackImages = this._stackImages = NumberHelpers.parseBooleanArgument(this.element.dataset.stackImages);

        this._registerEvents();

        this._globalMouseUpEvent = (event) => {
            this._buttonUp();
            event.preventDefault();
            return false;
        };
        this._screenSaver.onScreensaverEnabled(() => this.refresh());
        this.refresh();
    }

    private _registerEvents(): void {
        ['mousedown', 'touchstart'].forEach(
            (eventType) => this.element.addEventListener(eventType, (event: Event) => {
                this._buttonDown(event);
                event.preventDefault();
                event.stopPropagation();
                return false;
            }));

        ['mouseup', 'touchend', 'touchcancel'].forEach(
            (eventType) => this.element.addEventListener(eventType, (event: Event) => {
                this._buttonUp();
                event.preventDefault();
                event.stopPropagation();
                return false;
            }));
    }

    private _buttonDown(event: Event): void {
        if (event.type === 'mousedown') {
            document.addEventListener('click', this._globalMouseUpEvent);
        }

        this._isPressed = true;
        this.refresh();
    }
    private _buttonUp(): void {
        document.removeEventListener('click', this._globalMouseUpEvent);
        this._isPressed = false;
        this._screenSaver.activate();
        this.refresh();

    }
}
