export class Timer {

    private static _ticks: number = 0;
    private static _isStarted: boolean = false;

    public static start(): void {
        if (this._isStarted) {
            return;
        }
        this._isStarted = true;
        window.setInterval(() => Timer._incrementTicks(), 1000);

    }
    public static timestamp(): number {
        return Timer._ticks;
    }

    public static elapsedSeconds(ticks: number): number {
        return Timer._ticks - ticks;
    }

    private static _incrementTicks(): void {
        Timer._ticks++;
    }
}
