import { IValueConverter } from '../../../infrastructure/value-converter/value-converter';
import { ICommunicationHandler } from '../../../infrastructure/communication/communication-handler';
import { NumberHelpers } from '../helpers';
import { ValueType } from '../../../infrastructure/value-converter/value-types';
import { Component } from './component';
import { MfdStatusType, MfdStatusUpdateDataContent, StatusUpdateDataContent } from '../../../infrastructure/communication/protocol';
import { IMagnitudeConverter } from '../../../infrastructure/magnitude-converter';

export class SignalValue extends Component {
    private _value: number = 0;
    private _on: boolean = false;
    private _valueType: ValueType;
    private _formatMode: SignalValueFormatMode;
    private _formatSetting: number;
    private _decimalPlaces: number;
    private _showUnit: boolean;
    private _thresholdLowValue: number;
    private _thresholdHighValue: number;
    private _notAvailableValue: string;
    private _offValue: string;
    private _signalType: MfdStatusType = MfdStatusType.statusUpdate;
    private _valueConverter: IValueConverter = null;
    private _valueElement: HTMLElement = null;
    private _unitElement: HTMLElement = null;
    private formatter: Intl.NumberFormat;
    private _magnitudeConversion: number;
    private _magnitudeConverter: IMagnitudeConverter = null;


    constructor(element: HTMLElement, communicationHandler: ICommunicationHandler, valueConverter: IValueConverter, magnitudeConverter: IMagnitudeConverter) {
        super(element, communicationHandler);
        this._valueConverter = valueConverter;
        this._magnitudeConverter = magnitudeConverter;
    }

    public allowSignalTypes(): MfdStatusType[] {
        return [MfdStatusType.statusUpdate, MfdStatusType.dimmer];
    }
    public onMfdDataReceived(data: MfdStatusUpdateDataContent): void {

        if (data.type === MfdStatusType.statusUpdate) {
            this._valueType = data.valueTypeIdentifier;
            this._value = data.value;
        } else {
            this._valueType = ValueType.DATAITEM_GENERIC_DEC3;
            this._on = data.onOffStatus;
            this._value = data.dimmerValue * 100;
        }
        this.refresh();
    }

    public onNmeaDataReceived(data: StatusUpdateDataContent): void {
        this._value = data.value;
        this._valueType = data.valueTypeIdentifier;
        this.isUnavailable = data.unavailable;
        this.refresh();
    }

    public onRedraw(): void {
        if (this.isUnavailable) {
            this.drawIsUnavailable();
            return;
        }

        const convertedValue = this._valueConverter.getConvertedValue(this._value, this._valueType);
        let numberConvertedValue = convertedValue as number
        if (this._signalType === MfdStatusType.dimmer && !this._on && this._offValue !== 'value') {
            this._drawOffValue();
        } else if (this._decimalPlaces != null && !isNaN(numberConvertedValue)) {
            numberConvertedValue = this._magnitudeConverter.getConvertedValue(numberConvertedValue, this._magnitudeConversion);
            const roundedValue = this.formatter.format(numberConvertedValue);
            this._valueElement.textContent = roundedValue;
        } else {
            this._valueElement.textContent = convertedValue.toString();
        }

        if (this._showUnit) {
            const unitText = this._valueConverter.getUnit(this._valueType);
            this._unitElement.textContent = ' ' + unitText;
        }

        if (this._signalType === MfdStatusType.dimmer && !this._on) {
            this.element.classList.add('status-off');
            this.element.classList.remove('threshold-low');
            this.element.classList.remove('threshold-high');

        } else if (this._thresholdLowValue != null && this._thresholdLowValue >= this._value) {
            this.element.classList.add('threshold-low');
            this.element.classList.remove('threshold-high');
        } else if (this._thresholdHighValue != null && this._thresholdHighValue <= this._value) {
            this.element.classList.remove('threshold-low');
            this.element.classList.add('threshold-high');
        } else {
            this.element.classList.remove('threshold-low');
            this.element.classList.remove('threshold-high');
            this.element.classList.remove('status-off');
        }
    }

    protected onInitialize(): void {
        this._formatMode = this.element.dataset.formatMode as SignalValueFormatMode;
        this._formatSetting = parseInt(this.element.dataset.formatSetting, 10);
        this._decimalPlaces = parseInt(this.element.dataset.decimalPlaces, 10);
        this._showUnit = NumberHelpers.parseBooleanArgument(this.element.dataset.showUnit);
        this._thresholdLowValue = parseInt(this.element.dataset.thresholdLowValue, 10);
        this._thresholdHighValue = parseInt(this.element.dataset.thresholdHighValue, 10);
        this._notAvailableValue = this.element.dataset.notAvailableValue;
        this._offValue = this.element.dataset.offValue;
        this._signalType = parseInt(this.element.dataset.signalType, 10);
        this._magnitudeConversion = parseFloat(this.element.dataset.magnitudeConversion.replace(',', '.'));
        this._valueElement = this.element.querySelector('.value');
        this._unitElement = this.element.querySelector('.unit');
        this.formatter = this._createTextFormatter();
    }

    private drawIsUnavailable(): void {
        // Set to default color
        this.element.classList.remove('threshold-low');
        this.element.classList.remove('threshold-high');

        if (this._notAvailableValue === 'zero') {
            this._valueElement.textContent = this.formatter.format(0);
            if (this._showUnit) {
                const unitText = this._valueConverter.getUnit(this._valueType);
                this._unitElement.textContent = ' ' + unitText;
            }
            else {
                this._unitElement.textContent = '';
            }
        }
        else if (this._notAvailableValue === 'dash') {
            this._valueElement.textContent = '-';
            this._unitElement.textContent = '';
        }
        else if (this._notAvailableValue === 'empty') {
            this._valueElement.textContent = '';
            this._unitElement.textContent = '';
        }
        else if (this._notAvailableValue === 'natext') {
            this._valueElement.textContent = 'N/A';
            this._unitElement.textContent = '';
        }
        else {
            this._valueElement.textContent = '';
            this._unitElement.textContent = '';
        }
    }

    private _drawOffValue(): void {
        this._unitElement.textContent = '';
        if (this._offValue === 'zero') {
            this._valueElement.textContent = this.formatter.format(0);
        }
        else if (this._offValue === 'dash') {
            this._valueElement.textContent = '-';
            this._unitElement.textContent = '';
        }
        else if (this._offValue === 'empty') {
            this._valueElement.textContent = '';
        }
        else if (this._offValue === 'natext') {
            this._valueElement.textContent = 'N/A';
        }
        else {
            this._valueElement.textContent = '';
        }
    }

    private _createTextFormatter(): Intl.NumberFormat {
        let formatter: Intl.NumberFormat;
        if (this._formatMode === SignalValueFormatMode.decimals) {
            formatter = Intl.NumberFormat(undefined, { minimumFractionDigits: this._decimalPlaces, maximumFractionDigits: this._decimalPlaces, useGrouping: false });
        } else if (this._formatMode === SignalValueFormatMode.fixed) {
            const total = Math.max(this._formatSetting - this._decimalPlaces, 1);
            formatter = Intl.NumberFormat(undefined, { minimumIntegerDigits: total, minimumFractionDigits: this._decimalPlaces, maximumFractionDigits: this._decimalPlaces, useGrouping: false });
        } else {
            formatter = Intl.NumberFormat(undefined, { useGrouping: false });
        }
        return formatter;
    }
}

// eslint-disable-next-line no-shadow
export enum SignalValueFormatMode {
    decimals = 'decimals',
    fixed = 'fixed'
}
