/* eslint-disable @typescript-eslint/naming-convention */
/* Data Type	Value
SI Base/Derived	Units
Generic	0
Absolute Angle (M or T)	1	Derived from SI	radians (rad)
Atmospheric Pressure	2	SI Unit	Paschal (Pa)
Bearing (P or S)	3	Derived from SI	radians (rad)
Beaufort	4	Not SI	Beaufort number
Cardinal Wind	5	Derived from SI	radians (rad)
Current	6	Base	ampere (A)
Date	7	Number	Number, Days since 1/1/1970
Depth	8	SI Unit	Metre (m)
Dimensionless	9
Distance	10	SI Unit	Metre (m)
Frequency	11	Derived from SI	Hertz (Hz)
Fuel Economy	12		Q  Is Flow rate measured in (cu m/s) or (cu m/hr)?
Height	13	SI Unit	Metre (m)
Percentage	14		%
Position	15	Derived from SI	Degrees (deg)
Pressure	16	SI Unit	Paschal (Pa)
Range Bearing	17	?	Metre (m) & radians (rad)
Rate of Turn	18	Derived from SI	angular acceleration (rad/s)
RPM	19	Not SI	Rotation rate, RPM
Signed Angle (+ or -)	20	Derived from SI	radians (rad)
Speed	21	SI Unit	Metre per sec (m/s)
Temperature	22	SI Unit	kelvin (K)
Time	23	SI Unit	second (s)
Time Duration	24	SI Unit	second (s)
Transmission Gear	25	Not SI	Enumerated, 0,1,2,3 = neutral, forward, reverse, unknown
Unsigned Angle	26	Derived from SI	radians (rad)
Voltage	27	Derived from SI	volt (V)
Volume	28	SI Unit	Cubic metre cu m)
VolumeRate	29	SI Unit	(cu m/s
Wind Speed	30	derived	Metre per sec (m/s)
*/
export enum ValueType {
    // 0  In WDU.
    DATAITEM_GENERIC_DEC3 = 0,
    // 1  In WDU Not selectable in config As of 20180419.
    DATAITEM_ABS_ANGLE_M_OR_T_DEC3 = 1,
    // 2  In WDU
    DATAITEM_ATMOSPHERIC_PRESSURE_DEC3 = 2,
    // 3  In WDU Not selectable in config As of 20180419.
    DATAITEM_BEARING_P_OR_S = 3,
    // 4  In WDU Not selectable in config As of 20180419.
    DATAITEM_BEAUFORT = 4,
    // 5  In WDU Not selectable in config As of 20180419.
    DATAITEM_CARDINAL_WIND = 5,
    // 6  In WDU.
    DATAITEM_AMPERE_DEC3 = 6,
    // 7  In WDU.
    DATAITEM_DATE = 7,
    // 8  In WDU Not selectable in config As of 20180419.
    DATAITEM_DEPTH = 8,
    // 9  In WDU.
    DATAITEM_DIMENSIONLESS_DEC3 = 9,
    // 10 In WDU.
    DATAITEM_DISTANCE_DEC3 = 10,
    // 11 In WDU.
    DATAITEM_FREQUENCY_DEC3 = 11,
    // 12 In WDU.
    DATAITEM_FUELECONOMY_DEC3 = 12,
    // 13 In WDU. Not selectable in config As of 20180419
    DATAITEM_HEIGHT_DEC3 = 13,
    // 14 In WDU.
    DATAITEM_PERCENT_DEC3 = 14,
    // 15 In WDU. Not selectable in config As of 20180419
    DATAITEM_POSITION_DEC3 = 15,
    // 16 In WDU.
    DATAITEM_PRESSURE_DEC3 = 16,
    // 17 In WDU. Not selectable in config As of 20180419. STRANGE SPEC? Test with RYM Screen.
    DATAITEM_RANGEBEARING_DEC3 = 17,
    // 18 In WDU.
    DATAITEM_RATEOFTURN_RAD_P_SEC_DEC3 = 18,
    // 19 In WDU.
    DATAITEM_RPM_DEC3 = 19,
    // 20 In WDU.
    DATAITEM_SIGNEDANGLE_RAD_DEC3 = 20,
    // 21 In WDU.
    DATAITEM_SPEED_MET_PER_SEC_DEC3 = 21,
    // 22 In WDU.
    DATAITEM_TEMPERATURE_KELVIN_DEC3 = 22,
    // 23 In WDU.
    DATAITEM_TIME_SEC_DEC3 = 23,
    // 24 In WDU.
    DATAITEM_TIME_DURATION = 24,
    // 25 In WDU. Not selectable in config As of 20180419. This is enumeration. Neutral, Fwd, Rev, unknown
    DATAITEM_TRANSMISSION_GEAR_DEC3 = 25,
    // 26 In WDU.
    DATAITEM_UNSIGNEDANGLE_RAD_DEC3 = 26,
    // 27 In WDU.
    DATAITEM_VOLT_DEC3 = 27,
    // 28 In WDU.
    DATAITEM_CUMETRES_DEC3 = 28,
    // 29 In WDU.
    DATAITEM_VOLUMERATE_CUMS_DEC3 = 29,
    // 30 In WDU. Not selectable in config As of 20180419.
    DATAITEM_WINDSPEED_M_PER_S_DEC3 = 30,


    // /////Appendix B.3 Data Format is used. Data Format is defined DFXX(X) where uint16 value is XX(X) +100 ///////////


    // / @range: 0 to 2Pi rad
    // / @Resolution: 1x10E-4 rad
    // / @Type: uint16
    // / @Description: Resolution ~0.0057deg, 1 deg = .01745 rad
    DF02_ANGLE = 102,

    // DF03AnglePrecise = 103,
    // / @range: +/-Pi rad
    // / @Resolution: 1x10E-4 rad
    // / @Type: int16
    // / @Description: Resolution ~0.0057deg
    DF04_ANGLE_SIGNED = 104,

    // / @range: +/- 3,276.4 A
    // / @Resolution: 1x10E-1 A
    // / @Type: int16
    // / @Description:
    DF07_CURRENT_ELECTRIC_HIGH = 107,

    // / @range: 0 to ~4.295x10E+7 m
    // / @Resolution: 1x10E-2 m
    // / @Type: uint32
    // / @Description:
    DF09_DISTANCE = 109,

    // / @range: 0 to ~4.295x10E+7 m
    // / @Resolution: +/-3.2764 cu-m/hr
    // / @Type: int16
    // / @Description:
    DF18_FLOW_RATE_LOW = 118,

    // / @range:0 to 65,532,000 Pa
    // / @Resolution: 1x10E+3 Pa
    // / @Type: uint16
    // / @Description:
    DF29_PRESSURE = 129,

    // / @range:0 to 655.32 m/s
    // / @Resolution: 1 Knot = 0.5144 m/s
    // / @Type: uint16
    // / @Description:
    DF35_SPEED_OVER_GROUND = 135,

    // / @range: +/- 327.64 V
    // / @Resolution: 1x10E-2 V
    // / @Type: int16
    // / @Description:
    DF42_VOLTAGE_HIGH = 142,

    // / @range: 0 - 235,915,200 Coulombs
    // / @Resolution: 3600 C
    // / @Type: uint16
    // / @Description:
    DF100_BATTERY_CAPACITY = 200,

    // / @range: 0 - 65.532
    // / @Resolution: 1x10E-3 V
    // / @Type: uint16
    // / @Description:
    DF102_AC_VRMS = 202,

    // / @range: 0 to 16,777.212 deg K
    // / @Resolution: 0.001 deg K
    // / @Type: uint24
    // / @Description:
    DF105_TEMPERATURE_EXTENDED_RANGE_AND_PRECISION = 205,

    // / @range: 0 - 6553.2 V
    // / @Resolution: 0.1V
    // / @Type: uint16
    // / @Description:
    DF106_VOLTAGE = 206,

    // / @range: 0 - 2,520 meters
    // / @Resolution: 10 meters
    // / @Type: uint8
    // / @Description:
    DF109_DISTANCE_ROUGH = 209,

    // / @range: 0 - 100%
    // / @Resolution: 5x10E-1
    // / @Type: uint8
    // / @Description: 0 = 0%, 100 = 50%, 200 = 100%
    DF120_PERCENT_RESTRICTED_RANGE = 220,

    // / @range: 0-6553.2 kWh
    // / @Resolution: 0.1
    // / @Type: uint16
    // / @Description:
    DF123_BATTERY_CAPACITY_EXTENDED_RANGE = 223,

    // / @range: 0 -6553.2
    // / @Resolution: 1x10E-1 Nm
    // / @Type: uint16
    // / @Description:
    DF129_TORQUE = 229,

    // / @range: 0-655kW
    // / @Resolution: 10W
    // / @Type: uint16
    // / @Description:
    DF131_POWER_HIGH_RANGE = 231,

    // / @range: +/-838.8604 m/s*s
    // / @Resolution: 1x10-4 m/s*s
    // / @Type: int24
    // / @Description:
    DF30_PERCENT_RELATICE_MEASURE = 130,

    // / @range: +/- 327.64 dB
    // / @Resolution: 1x10E-2 dB
    // / @Type: int16
    // / @Description:
    DF38_TEMPERATURE_HIGH = 138,
    // / @range: 0 to 655.32 deg K
    // / @Resolution: 1x10E-2 deg K
    // / @Type: uint16
    // / @Description:
    DF39_TEMPERATURE_LOW = 139,

    // / @range: +/- ~2.148x10E+6 s
    // / @Resolution: 1x10E-3 s
    // / @Type: int32
    // / @Description:
    DF44_VOLUME = 144,

    // / @range: +/- 32.764 m
    // / @Resolution: 1x10E-3 m
    // / @Type: int16
    // / @Description:
    DF46_DISTANCE_SIGNED_MEDIUM = 146,

    // / @range: 0 to 6,553,200 Pa
    // / @Resolution: 1x10E+2 Pa
    // / @Type: uint16
    // / @Description:
    DF47_PRESSURE_MEDIUM = 147,

    // / @range:  Variable
    // / @Resolution: 1
    // / @Type: bit(n)
    // / @Description: Used to construct bit fields
    DF52_BIT_FIELD = 152,
    // / @range: 0 to 252
    // / @Resolution: 1 bit
    // / @Type: uint8
    // / @Description: Unit-less number
    DF53_INTEGER_8_BIT = 153,

    // / @range: 0 to 65,532
    // / @Resolution: 1 bit
    // / @Type: uint16
    // / @Description:
    DF54_INTEGER_16_BIT_UNSIGNED = 154,
    // / @range: 0 to 4,294,967,292
    // / @Resolution: 1 bit
    // / @Type: uint32
    // / @Description: Unit-less number
    DF55_INTEGER_32_BIT = 155,
    // / @range: 0 to (2E+64)-4
    // / @Resolution: 1 bit
    // / @Type: uint64
    // / @Description: Unit-less number
    DF56_INTEGER_64_BIT = 156,
    // / @range: -128 to +124
    // / @Resolution: 1 bit
    // / @Type: int8
    // / @Description: Unit-less number
    DF57_INTEGER_8_BIT_2S_COMPLEMENT = 157,
    // / @range: -32,768 to +32,764
    // / @Resolution: 1 bit
    // / @Type: int16
    // / @Description:
    DF58_INTEGER_16_BITS_2S_COMPLEMENT = 158,
    // / @range: -2,147,483,648 to +2,147,483,644
    // / @Resolution: 1 bit
    // / @Type: int32
    // / @Description: Unit-less number
    DF59_INTEGER_32_BIT_2S_COMPLEMENT = 159,
    // / @range: -(2E+63) to +(2E+63)-4
    // / @Resolution: 1 bit
    // / @Type: int64
    // / @Description:
    DF60_INTEGER_64_BIT_2S_COMPLEMENT = 160,

    // / @range: 0 to ~4.295x10E+6 s
    // / @Resolution: 1x10E-3 s
    // / @Type: uint32
    // / @Description:
    DF65_TIME_INTERVAL_STANDARD = 165,

    // / @range: 0 to ~4.295x10E+9 s
    // / @Resolution: 1 sec
    // / @Type: uint32
    // / @Description:
    DF67_TIME_INTERVAL_LARGE = 167,

    // / @range: +/-32,764 minutes
    // / @Resolution: 1.0 minute
    // / @Type: int16
    // / @Description:
    DF71_TIME_INTERVAL_MEDIUM = 171,
    // / @range: 0-16,383 RPM
    // / @Resolution: 1/4 RPM
    // / @Type: uint16
    // / @Description:
    DF72_ROTATIONAL_RATE_UNSIGNED = 172,

    // / @range: 0 - 252%
    // / @Resolution: 1%
    // / @Type: uint8
    // / @Description:
    DF93_PERCENT_ABSOLUTE = 193,

    // / @range: 0 - 6553.2 A
    // / @Resolution: 1x10E-1 A
    // / @Type: uint16
    // / @Description:
    DF95_CURRENT_ELECTRIC_HIGH = 195,

    // / @range: 0 - 65,532 minutes
    // / @Resolution: 1 minute
    // / @Type: uint16
    // / @Description:
    DF98_TIME_INTERVAL_MEDIUM = 198


}

