    SPEED_MET_PER_SEC_DEC0,
    SPEED_KNOTS,
    SPEED_MILES_PER_HOUR,
    SPEED_KM_PER_HOUR,
    TEMPERATURE_CELCIUS,
    TEMPERATURE_FAHRENHEIT,
    TEMPERATURE_KELVIN,
    TIME_SEC_SECOND,
    TIME_DURATION_STRING,
    TRANSMISSION_GEAR_DEC0,
    UNSIGNEDANGLE_RAD_DEC0,
    VOLUMERATE_CUMS_DEC0,
    VOLUMERATE_GALS,
