import { LanguageHandler } from './language-handler';
import { ValueConverter } from '../../infrastructure/value-converter/value-converter';
import { WebSocketManager } from '../../infrastructure/communication/web-socket-manager';
import { ComponentFactory } from './component-factory';
import { PageNavigator } from './page-navigator';
import { SettingsReader } from '../../infrastructure/settings-reader';
import { CommunicationHandler } from '../../infrastructure/communication/communication-handler';
import { PageOverlays } from './page-overlays';
import { DataSubscription } from './data-subscription';
import { ViewPortManager } from './view-port-manager';
import { ScreenSaver } from './screen-saver';
import { Log } from '../../infrastructure/log';
import { Timer } from '../../infrastructure/timer';
import { WebSocketContentDecoder } from '../../infrastructure/communication/web-socket-content-decoder';
import { ApplicationSettings } from './application-settings';
import { NetlistChecksumHandler } from '../../infrastructure/netlist-checksum-handler';
import { MagnitudeConverter } from '../../infrastructure/magnitude-converter';
import { AppSelectorLinkTimerDialog } from './app-selector-link-timer-dialog';



export class GraphicsBootstrapper {

    public async initialize(): Promise<void> {
        const webSocketManager = new WebSocketManager();
        const contentDecoder = new WebSocketContentDecoder();
        await contentDecoder.initialize(true);
        const communicationHandler = new CommunicationHandler(webSocketManager, contentDecoder);
        const settingsReader = new SettingsReader(communicationHandler);
        await settingsReader.load();
        const valueConverter = new ValueConverter(settingsReader);
        const magnitudeConverter = new MagnitudeConverter();
        const applicationSettings = ApplicationSettings.readApplicationSettings();
        const languageHandler = new LanguageHandler(settingsReader, applicationSettings);
        const pageNavigator = new PageNavigator();
        const appSelectorLinkTimerDialog = new AppSelectorLinkTimerDialog(pageNavigator);
        appSelectorLinkTimerDialog.initialize();
        const screenSaver = new ScreenSaver(settingsReader, pageNavigator);
        const componentFactory = new ComponentFactory(valueConverter, pageNavigator, communicationHandler, screenSaver, magnitudeConverter);
        const [pages, viewPorts] = componentFactory.create();
        pageNavigator.setPages(pages);
        screenSaver.initialize();
        const viewPortManager = new ViewPortManager(pageNavigator, screenSaver);
        viewPortManager.setViewPorts(viewPorts);
        const netlistChecksumHandler = new NetlistChecksumHandler(communicationHandler);
        await netlistChecksumHandler.initialize();
        const pageOverlays = new PageOverlays(communicationHandler, netlistChecksumHandler);
        pageOverlays.initialize();
        Timer.start();
        // Start websocket
        webSocketManager.connect();
        // Start data subscription
        const dataSubscription = new DataSubscription(webSocketManager);
        dataSubscription.start();

        await languageHandler.initialize();
        // Log.logLevel = LogLevel.info;
        // Log.autoRefresh = true;
        Log.initialize();

        // Hide startup logger button
        onSystemHasStarted();
        // Log.showLogView();
    }

}
