import { HtmlHelpers, NumberHelpers } from '../helpers';
import { MfdStatusType, MfdStatusUpdateDataContent, Protocol } from '../../../infrastructure/communication/protocol';
import { ICommunicationHandler } from '../../../infrastructure/communication/communication-handler';
import { throttle, debounce } from 'throttle-debounce';
import { Component } from './component';

export class Slider extends Component {
    private _error1: boolean = false;
    private _error2: boolean = false;
    private _on: boolean = false;
    private _minValue: number;
    private _maxValue: number;
    private _rotation: number;
    private _readOnly: boolean;
    private _moving: boolean;
    private _trackFillElement: HTMLElement;
    private _error1Element: HTMLElement;
    private _error2Element: HTMLElement;
    private _error12Element: HTMLElement;
    private _notAvailableElement: HTMLElement;
    private _handleElement: HTMLElement;
    private _handlePressedElement: HTMLElement;
    private _handleOnElement: HTMLElement;
    private _handleError1Element: HTMLElement;
    private _handleError2Element: HTMLElement;
    private _handleError12Element: HTMLElement;
    private _handleNotAvailableElement: HTMLElement;
    // private _hideOnServiceUnavailable: boolean = false;
    private _globalMouseUpEvent: (event: MouseEvent) => boolean;
    private _globalMouseMoveEvent: (event: MouseEvent) => boolean;
    private _throttleSendData: (value: number) => void;
    private _throttleRefresh: () => void;
    private _debounceRefreshByFeedBackValue: () => void;
    private _value: number;
    private _feedBackValue: number;
    constructor(element: HTMLElement, communicationHandler: ICommunicationHandler) {
        super(element, communicationHandler);
    }

    public allowSignalTypes(): MfdStatusType[] {
        return [MfdStatusType.dimmer];
    }

    public registerEvents(): void {
        ['mousedown', 'touchstart'].forEach((eventType) => this.element.addEventListener(eventType, (event) => {
            this.onButtonDown(event);
            event.stopPropagation();
            ['mousemove', 'touchmove'].forEach(
                (innerEventType) =>
                    document.addEventListener(innerEventType, this._globalMouseMoveEvent)
            );
            ['mouseup', 'touchend', 'touchcancel'].forEach(
                (innerEventType) =>
                    document.addEventListener(innerEventType, this._globalMouseUpEvent)
            );
            event.preventDefault();
            return false;
        }));
        ['mouseup', 'touchend', 'touchcancel'].forEach(
            (eventType) => this.element.addEventListener(eventType, (event: Event) => {
                this.onButtonUp(event);
                event.preventDefault();
                return false;
            }));

        window.addEventListener('resize', () => { this._throttleRefresh(); });
    }

    public onUnavailableChanged(): void {
        if (this.isUnavailable) {
            this.onButtonUp(null);
        }
    }

    public onButtonDown(event: any): void {
        if (this._readOnly || this.isUnavailable) {
            this._moving = false;
            return;
        }

        this._moving = true;
        const evt = event.originalEvent === undefined ? event : event.originalEvent;
        let x = 0;
        let y = 0;
        if (evt.type === 'touchstart') {
            x = evt.touches[0].pageX;
            y = evt.touches[0].pageY;
        } else {
            x = evt.pageX;
            y = evt.pageY;
        }

        this.updatePosition(x, y);
    }

    public onButtonUp(_event: Event): void {
        this._moving = false;
        this._debounceRefreshByFeedBackValue();
        this._throttleRefresh();
        ['mousemove', 'touchmove'].forEach((eventType) => document.removeEventListener(eventType, this._globalMouseMoveEvent));
        ['mouseup', 'touchend', 'touchcancel'].forEach((eventType) => document.removeEventListener(eventType, this._globalMouseUpEvent));
    }

    public onButtonMove(event: any): void {
        if (this._readOnly || this.isUnavailable) {
            this._moving = false;
            return;
        }

        const evt = event.originalEvent === undefined ? event : event.originalEvent;
        let x = 0;
        let y = 0;

        if (evt.type === 'touchmove') {
            x = evt.touches[0].pageX;
            y = evt.touches[0].pageY;
        } else {
            x = evt.pageX;
            y = evt.pageY;
        }

        this.updatePosition(x, y);
    }

    public updatePosition(rawX: number, rawY: number): void {
        if (this._handleElement == null) {
            return;
        }

        const elementBoundingClientRect = this.element.getBoundingClientRect();
        const xCenter = elementBoundingClientRect.left + elementBoundingClientRect.width / 2;
        const yCenter = elementBoundingClientRect.top + elementBoundingClientRect.height / 2;
        const xFromCenter = rawX - xCenter;
        const yFromCenter = rawY - yCenter;

        const minActualWidth = 0; // To be used in the future if we want to restrict the slider area.
        const handleWidth = parseFloat(getComputedStyle(this._handleElement).getPropertyValue('width'));
        const elementWidth = parseFloat(getComputedStyle(this.element).getPropertyValue('width'));

        let rotatedX = xFromCenter * Math.cos(-this._rotation * Math.PI / 180)
            - yFromCenter * Math.sin(-this._rotation * Math.PI / 180);
        rotatedX += elementWidth / 2;

        rotatedX -= handleWidth / 2;
        const maxActualWidth = (elementWidth - handleWidth);
        if (rotatedX < minActualWidth) {
            rotatedX = minActualWidth;
        }
        else if (rotatedX > maxActualWidth) {
            rotatedX = maxActualWidth;
        }

        const value = (((rotatedX - minActualWidth) / (maxActualWidth - minActualWidth)) * (this._maxValue - this._minValue)) + this._minValue;

        this.setValue(value);
        this._throttleRefresh();
    }

    public setValue(value: number): void {
        this._value = value;
        this._throttleSendData(value);
    }
    public sendData(value: number): void {
        this.communicationHandler.send(Protocol.dimmerCommand(this.mfdSignalId, value, 0x00));
    }

    public onRedraw(): void {
        if (this._handleElement == null) {
            return;
        }

        const minActualWidth = 0; // To be used in the future if we want to restrict the slider area.
        const handleWidth = parseFloat(getComputedStyle(this._handleElement).getPropertyValue('width'));
        const elementWidth = parseFloat(getComputedStyle(this.element).getPropertyValue('width'));
        const maxActualWidth = elementWidth - handleWidth;

        // Clamping of value
        const clampedValue = this._clampValue();

        // Horizontal positioning of handle
        const handlePosition = ((clampedValue - this._minValue) / (this._maxValue - this._minValue)) * (maxActualWidth - minActualWidth) + minActualWidth;
        const xPx = `${handlePosition}px`;

        this._setLeft(xPx, this._handleElement);
        this._setLeft(xPx, this._handleOnElement);
        this._setLeft(xPx, this._handlePressedElement);
        this._setLeft(xPx, this._handleError1Element);
        this._setLeft(xPx, this._handleError2Element);
        this._setLeft(xPx, this._handleError12Element);
        this._setLeft(xPx, this._handleNotAvailableElement);

        // Vertical centering of handle
        const elementHeight = parseFloat(getComputedStyle(this.element).getPropertyValue('height'));
        const handleHeight = parseFloat(getComputedStyle(this._handleElement).getPropertyValue('height'));
        const y = elementHeight / 2 - handleHeight / 2;
        const yPx = `${y}px`;
        this._setTop(yPx, this._handleElement);
        this._setTop(yPx, this._handleOnElement);
        this._setTop(yPx, this._handlePressedElement);
        this._setTop(yPx, this._handleError1Element);
        this._setTop(yPx, this._handleError2Element);
        this._setTop(yPx, this._handleError12Element);
        this._setTop(yPx, this._handleNotAvailableElement);

        const trackOnWidth = ((clampedValue - this._minValue) / (this._maxValue - this._minValue)) * elementWidth;

        if (this._trackFillElement != null) {
            this._trackFillElement.style.clip = `rect(0px, ${trackOnWidth}, ${elementHeight}, 0px)`;
        }

        HtmlHelpers.setElementVisibility(this._handlePressedElement, this._moving);
        HtmlHelpers.setElementVisibility(this._handleOnElement, this._on);
        HtmlHelpers.setElementVisibility(this._error1Element, this._error1);
        HtmlHelpers.setElementVisibility(this._error2Element, this._error2);
        HtmlHelpers.setElementVisibility(this._error12Element, this._error1 && this._error2);
        HtmlHelpers.setElementVisibility(this._notAvailableElement, this.isUnavailable);

        HtmlHelpers.setElementVisibility(this._handleError1Element, this._error1);
        HtmlHelpers.setElementVisibility(this._handleError2Element, this._error2);
        HtmlHelpers.setElementVisibility(this._handleError12Element, this._error1 && this._error2);
        HtmlHelpers.setElementVisibility(this._handleNotAvailableElement, this.isUnavailable);

    }

    public onMfdDataReceived(dimmerBaseMessage: MfdStatusUpdateDataContent): void {
        this._error1 = dimmerBaseMessage.err1FT;
        this._error2 = dimmerBaseMessage.err2UC;
        this._on = dimmerBaseMessage.onOffStatus;
        this._feedBackValue = dimmerBaseMessage.dimmerValue;

        if (!this._moving) {
            this._value = this._feedBackValue;
            this._throttleRefresh();
        }
    }

    protected onInitialize(): void {
        this._rotation = (this.element.dataset.rotation == null) ? 0 : parseFloat(this.element.dataset.rotation);
        this._readOnly = NumberHelpers.parseBooleanArgument(this.element.dataset.readOnly);
        this._minValue = parseInt(this.element.dataset.minValue, 10);
        this._maxValue = parseInt(this.element.dataset.maxValue, 10);
        this.registerEvents();
        this._moving = false;
        this._trackFillElement = this.element.querySelector('.slider-track-fill');
        this._error1Element = this.element.querySelector('.slider-error1');
        this._error2Element = this.element.querySelector('.slider-error2');
        this._error12Element = this.element.querySelector('.slider-error12');
        this._notAvailableElement = this.element.querySelector('.slider-service-unavailable');

        this._handleElement = this.element.querySelector('.slider-handle-bg');
        this._handlePressedElement = this.element.querySelector('.slider-handle-pressed');
        this._handleOnElement = this.element.querySelector('.slider-handle-on');
        this._handleError1Element = this.element.querySelector('.slider-handle-error1');
        this._handleError2Element = this.element.querySelector('.slider-handle-error2');
        this._handleError12Element = this.element.querySelector('.slider-handle-error12');
        this._handleNotAvailableElement = this.element.querySelector('.slider-handle-service-unavailable');

        this._globalMouseUpEvent = (event: MouseEvent) => {
            this.onButtonUp(event);
            event.preventDefault();
            return false;
        };
        this._globalMouseMoveEvent = (event: MouseEvent) => {
            if (this._moving) {
                this.onButtonMove(event);
                event.preventDefault();
                return false;
            }
            event.preventDefault();
            return true;
        };

        this._throttleSendData = throttle(250, (val: number) => { this.sendData(val); });
        this._throttleRefresh = throttle(50, () => { this.refresh(); });
        this._debounceRefreshByFeedBackValue = debounce(2000, () => {
            this._value = this._feedBackValue;
            this._throttleRefresh();
        });
        this._value = this._minValue;
    }

    private _clampValue(): number {
        let clampedValue = this._value;
        if (clampedValue < this._minValue) {
            clampedValue = this._minValue;
        }
        else if (clampedValue > this._maxValue) {
            clampedValue = this._maxValue;
        }
        return clampedValue;
    }

    private _setTop(yPx: string, element: HTMLElement): void {
        if (element != null) {
            element.style.top = yPx;
        }
    }

    private _setLeft(xPx: string, element: HTMLElement): void {
        if (element != null) {
            element.style.left = xPx;
        }
    }
}
