
/* eslint-disable no-console */
// eslint-disable-next-line no-shadow
export enum LogLevel {
    off = 0,
    error = 1,
    warning = 2,
    info = 3,
    verbose = 4,
    debug = 5
}

export class Log {

    private static _clickStart: Date = new Date();
    private static _lastClick: Date = new Date();
    private static _isInitialized: boolean = false;
    private static _debugModeActive: boolean = false;
    private static _activateDuration: number = 2000;
    private static _requiredShortPressCount: number = 3;
    private static _shortPressCount: number = 0;
    private static _state: number = 0;
    public static logLevel: LogLevel = LogLevel.error;
    public static autoRefresh: boolean = false;
    public static logError(message?: any, ...optionalParams: any[]): void {
        if (this.logLevel >= LogLevel.error) {
            console.error(message, ...optionalParams);
            Log._checkAutoRefresh()
        }
    }

    private static _checkAutoRefresh(): void {
        if (Log.autoRefresh) {
            Log._populateLog();
        }
    }
    public static logWarning(message?: any, ...optionalParams: any[]): void {
        if (Log.logLevel >= LogLevel.warning) {
            console.log(message, ...optionalParams);
            Log._checkAutoRefresh()
        }
    }
    public static logInfo(message?: any, ...optionalParams: any[]): void {
        if (Log.logLevel >= LogLevel.info) {
            console.log(message, ...optionalParams);
            Log._checkAutoRefresh()
        }
    }
    public static logVerbose(message?: any, ...optionalParams: any[]): void {
        if (Log.logLevel >= LogLevel.verbose) {
            console.log(message, ...optionalParams);
            Log._checkAutoRefresh()
        }
    }
    public static logDebug(message?: any, ...optionalParams: any[]): void {
        if (Log.logLevel >= LogLevel.debug) {
            console.log(message, ...optionalParams);
            Log._checkAutoRefresh()
        }
    }

    public static initialize(): void {
        if (Log._isInitialized === true) {
            return;
        }
        Log._isInitialized = true;

        document.addEventListener('mousedown', (_event: MouseEvent) => Log._handleStartLogView(), true);
        document.addEventListener('keydown', (event: KeyboardEvent) => {
            if (Log._debugModeActive) {
                return;
            }

            if (event.ctrlKey && event.altKey && event.key === 'l') {
                Log.showLogView();
            }
        });
    }

    public static showLogView(): void {
        Log._debugModeActive = true;
        let loggerDiv = document.querySelector<HTMLElement>('.log-view-wrapper');

        // Create log view div
        if (loggerDiv == null) {
            loggerDiv = Log._setupLogView();
        }
        Log._populateLog();
        loggerDiv.style.display = 'block';
    }

    private static _closeLog(): void {
        Log._state = 0;
        Log._shortPressCount = 0;
        Log._debugModeActive = false;
        Log.logLevel = LogLevel.error;
        const loggerDiv = document.querySelector<HTMLElement>('.log-view-wrapper');
        loggerDiv.style.display = 'none';
        const loggerContent = document.querySelector<HTMLElement>('.log-view-content');
        loggerContent.innerHTML = '';
    }

    private static _clearLog(): void {
        globalLogMessages.splice(0);
        this._populateLog();
    }

    private static _maximizeLog(): void {

        const loggerView = document.querySelector<HTMLElement>('.log-view');
        loggerView.style.display = 'block';
        const maximizeButton = document.querySelector<HTMLElement>('.maximize-log-button');
        maximizeButton.style.display = 'none';
    }

    private static _mimimizeLog(): void {

        const loggerView = document.querySelector<HTMLElement>('.log-view');
        loggerView.style.display = 'none';
        const maximizeButton = document.querySelector<HTMLElement>('.maximize-log-button');
        maximizeButton.style.display = 'block';
    }


    private static _setAutoRefreshLog(): void {

        const button = document.querySelector<HTMLInputElement>('.auto-refresh-log-button');
        if (Log.autoRefresh === true) {
            Log.autoRefresh = false;
            button.innerText = 'Turn Auto Refresh On';
        } else {
            Log.autoRefresh = true;
            button.innerText = 'Turn Auto Refresh Off';
        }
    }


    private static _handleStartLogView(): void {

        if (Log._debugModeActive) {
            Log._lastClick = new Date();
            return;
        }

        if (Log._durationGreaterThan(3 * Log._activateDuration, Log._lastClick)) {
            Log._state = 0;
            Log._shortPressCount = 0;
        }

        if (Log._state % 2 !== 0) { // Wait mode
            if (Log._durationGreaterThan(Log._activateDuration, Log._lastClick)) {
                Log._state++;
            } else {
                Log._state = 0;
            }
            Log._shortPressCount = 0;
        }

        if (Log._state % 2 === 0) {  // Click mode
            if (Log._shortPressCount === 0) {
                Log._clickStart = new Date();
            }
            if (Log._shortPressCount < Log._requiredShortPressCount && Log._durationGreaterThan(Log._activateDuration, Log._clickStart)) {
                Log._state = 0; // Sequence failed
                Log._shortPressCount = 0;
                return;
            } else {
                Log._shortPressCount++;
            }
            if (Log._shortPressCount >= Log._requiredShortPressCount) {
                Log._state++;
                Log._clickStart = new Date();
            }
        }
        Log._lastClick = new Date();

        if (Log._state > 6) {
            Log._state = 0;
            Log._shortPressCount = 0;
            Log.showLogView();
        }
    }

    private static _durationGreaterThan(limit: number, timeToCompare: Date): boolean {
        return new Date().getTime() - timeToCompare.getTime() > limit;
    }

    private static _setupLogView(): HTMLElement {
        const body = document.querySelector('body') as HTMLElement;
        const loggerDiv = document.createElement('div');
        loggerDiv.classList.add('log-view-wrapper');

        loggerDiv.innerHTML = `
        <button class="maximize-log-button">Maximize</button>
        <div class="log-view">
            <div class="log-view-toobar">
            <button class="close-log-button log-button">Exit</button>
            <button class="clear-log-button log-button">Clear</button>
            <button class="minimize-log-button log-button">Minimize</button>
            <button class="refresh-log-button log-button">Refresh</button>
            <button class="auto-refresh-log-button log-button">${Log.autoRefresh === false ? 'Turn Auto Refresh On' : 'Turn Auto Refresh Off'}</button>
            <select class="log-level-selector log-button">
                <option value="5" ${Log.logLevel === LogLevel.debug ? 'selected' : ''}>Debug</option>
                <option value="4" ${Log.logLevel === LogLevel.verbose ? 'selected' : ''}>Verbose</option>
                <option value="3" ${Log.logLevel === LogLevel.info ? 'selected' : ''}>Info</option>
                <option value="2" ${Log.logLevel === LogLevel.warning ? 'selected' : ''}>Warning</option>
                <option value="1" ${Log.logLevel === LogLevel.error ? 'selected' : ''} >Error</option>
                <option value="0" ${Log.logLevel === LogLevel.off ? 'selected' : ''}>Off</option>
            </select>
            </div>
            <div class="log-view-content">
            </div>
        </div>`;
        body.appendChild(loggerDiv);

        const logLevelSelect = document.querySelector<HTMLSelectElement>('.log-level-selector');
        logLevelSelect.onchange = (event: Event) => {
            const selector = event.target as HTMLSelectElement
            Log.logLevel = parseInt(selector.value, 10) as LogLevel;
        }
        const closeLogButton = document.querySelector<HTMLButtonElement>('.close-log-button');
        closeLogButton.onclick = () => Log._closeLog();

        const clearLogButton = document.querySelector<HTMLButtonElement>('.clear-log-button');
        clearLogButton.onclick = () => Log._clearLog();

        const minimizeLogButton = document.querySelector<HTMLButtonElement>('.minimize-log-button');
        minimizeLogButton.onclick = () => Log._mimimizeLog();

        const maximizeLogButton = document.querySelector<HTMLButtonElement>('.maximize-log-button');
        maximizeLogButton.onclick = () => Log._maximizeLog();

        const refreshLogButton = document.querySelector<HTMLButtonElement>('.refresh-log-button');
        refreshLogButton.onclick = () => Log._populateLog();

        const autoRefreshLogInput = document.querySelector<HTMLButtonElement>('.auto-refresh-log-button');
        autoRefreshLogInput.onclick = () => Log._setAutoRefreshLog();
        return loggerDiv;
    }
    private static _populateLog(): void {
        const loggerContent = document.querySelector<HTMLElement>('.log-view-content');
        loggerContent.innerHTML = '';
        const length = globalLogMessages.length;
        for (let i = length - 1; i >= 0; i--) {
            const message = globalLogMessages[i];
            const node = document.createElement('div');
            const textNode = document.createTextNode(message);
            node.appendChild(textNode);
            loggerContent.appendChild(node);
        }
    }
}
