import { FileLoadHelper } from '../file-load-helper';
import { WebsocketMessage, StatusUpdateDataContent, MfdStatusUpdateDataContent, MfdStatusCommandTypes, NmeaMessageCommandTypes, MfdStatusType, ValueDataType } from './protocol';
import { SignalInfo } from '../signals/signal-info';
import { SignalInfoType } from '../signals/signal-info-type';
import { VirtualSignalInfo } from '../signals/virtual-signal-info';


export class WebSocketContentDecoder {

    public get useChannelItems(): boolean {
        return this._useChannelItems;
    }
    private _useChannelItems: boolean;
    private _nmeaSignalInfoMap: Map<number, SignalInfo> = new Map<number, SignalInfo>();
    private _virtualSignalInfoMap: Map<number, VirtualSignalInfo[]> = new Map<number, VirtualSignalInfo[]>();

    public decodeN2KDataItem(wsMessage: WebsocketMessage): StatusUpdateDataContent {
        if (wsMessage.messagecmd === NmeaMessageCommandTypes.statusUpdate) {
            const signalId = (wsMessage.data[0] | (wsMessage.data[1] << 8));
            const nmeaStatusContent: StatusUpdateDataContent = new StatusUpdateDataContent();
            nmeaStatusContent.signalId = signalId;
            nmeaStatusContent.unavailable = (wsMessage.data[2] & 0x80) === 0 ? false : true;
            nmeaStatusContent.valueTypeIdentifier = (wsMessage.data[3] | (wsMessage.data[4] << 8));
            nmeaStatusContent.value = this._getValue(signalId, wsMessage.data, 5);
            return nmeaStatusContent;
        }
        return null;
    }

    public decodeMFDChannelItem(wsMessage: WebsocketMessage): MfdStatusUpdateDataContent {
        const signalId = wsMessage.data[0] | (wsMessage.data[1] << 8);
        const content: MfdStatusUpdateDataContent = new MfdStatusUpdateDataContent();
        content.signalId = signalId;

        if (wsMessage.messagecmd === MfdStatusCommandTypes.statusUpdate) {
            content.signalId = signalId;
            content.type = MfdStatusType.statusUpdate;
            content.valueTypeIdentifier = wsMessage.data[3];
            content.value = this._getInt32Value(signalId, wsMessage.data, 4);

            // content.value = (wsMessage.data[7] << 24) | (wsMessage.data[6] << 16) | (wsMessage.data[5] << 8) | (wsMessage.data[4] << 0);
            content.unavailable = (wsMessage.data[2] & 0x80) === 0 ? false : true;
            return content;
        } else if (wsMessage.messagecmd === MfdStatusCommandTypes.toggle) {
            content.type = MfdStatusType.pulse; // This is the base type. Checked by receiver that it matches!
            content.onOffStatus = (wsMessage.data[2] & 0x01) === 0 ? false : true;
            content.err1FT = (wsMessage.data[2] & 0x02) === 0 ? false : true;
            content.err2UC = (wsMessage.data[2] & 0x08) === 0 ? false : true;
            content.unavailable = (wsMessage.data[2] & 0x80) === 0 ? false : true;
            return content;
        } else if (wsMessage.messagecmd === MfdStatusCommandTypes.momentary) {
            content.type = MfdStatusType.momentary; // This is the base type. Checked by receiver that it matches!
            content.onOffStatus = (wsMessage.data[2] & 0x01) === 0 ? false : true;
            content.err1FT = (wsMessage.data[2] & 0x02) === 0 ? false : true;
            content.err2UC = (wsMessage.data[2] & 0x08) === 0 ? false : true;
            content.unavailable = (wsMessage.data[2] & 0x80) === 0 ? false : true;
            return content;
        } else if (wsMessage.messagecmd === MfdStatusCommandTypes.dimmerUpdate) {
            content.type = MfdStatusType.dimmer; // This is the base type. Checked by receiver that it matches!
            content.onOffStatus = (wsMessage.data[2] & 0x01) === 0 ? false : true;
            content.err1FT = (wsMessage.data[2] & 0x02) === 0 ? false : true;
            content.err2UC = (wsMessage.data[2] & 0x08) === 0 ? false : true;
            content.unavailable = (wsMessage.data[2] & 0x80) === 0 ? false : true;
            content.dimmerValue = (wsMessage.data[3] | (wsMessage.data[4] << 8));
            return content;
        } else {
            return null;
        }
    }

    public getVirtualSignals(sourceSignalId: number): VirtualSignalInfo[] {
        const res = this._virtualSignalInfoMap.get(sourceSignalId);
        return res != null ? res : [];
    }

    private _getInt32Value(_signalId: number, source: number[], startIndex: number): number {
        const buffer = new Uint8Array(source);
        const dataView2 = new DataView(buffer.buffer);
        return dataView2.getInt32(startIndex, true);
    }

    private _getValue(signalId: number, source: number[], startIndex: number): number {
        const buffer = new Uint8Array(source);
        const dataView2 = new DataView(buffer.buffer);
        let dataType: ValueDataType = ValueDataType.int32;

        if (this.useChannelItems) {
            const mapDt = this._nmeaSignalInfoMap.get(signalId);
            dataType = mapDt != null ? mapDt.dataType : ValueDataType.int32;
        }
        switch (dataType) {
            case ValueDataType.int8:
                return dataView2.getInt8(startIndex);
            case ValueDataType.uint8:
                return dataView2.getUint8(startIndex);
            case ValueDataType.int16:
                return dataView2.getInt16(startIndex, true);
            case ValueDataType.uint16:
                return dataView2.getUint16(startIndex, true);
            case ValueDataType.int32:
                return dataView2.getInt32(startIndex, true);
            case ValueDataType.uint32:
                return dataView2.getUint32(startIndex, true);
            case ValueDataType.int64:
                return new Number(dataView2.getBigInt64(startIndex, true)).valueOf(); // The will probably not work for large values
            case ValueDataType.uint64:
                return new Number(dataView2.getBigUint64(startIndex, true)).valueOf(); // The will probably not work for large values
            case ValueDataType.bit:
                return dataView2.getUint8(startIndex) & 0x01;
        }
    }

    public async initialize(useChannelItemsFile: boolean): Promise<void> {
        this._useChannelItems = useChannelItemsFile;
        if (useChannelItemsFile) {
            const file = await FileLoadHelper.getJsonFile('signal-info.json') as SignalInfo[];
            if (file != null) {
                for (const info of file.filter(i => i.type === SignalInfoType.nmea)) {
                    this._nmeaSignalInfoMap.set(info.signalId, info);
                }

                for(const info of file.filter(i => i.type === SignalInfoType.virtual)) {
                    const sourceSignalId = (info as VirtualSignalInfo).sourceSignalId;
                    if(this._virtualSignalInfoMap.has(sourceSignalId)){
                        const existingSignals = this._virtualSignalInfoMap.get(sourceSignalId) ;
                        existingSignals.push(info as VirtualSignalInfo);
                    } else {
                        this._virtualSignalInfoMap.set(sourceSignalId, [info as VirtualSignalInfo]);
                    }
                }
            }
        }
    }
}
