export class ExternalPageNavigator {
    private _externalPageBackButton: HTMLElement;
    private _onHideCallback: () => void;
    constructor() {
        document.querySelector('.external-page').classList.add('hidden');
        this._externalPageBackButton = document.querySelector('.external-page-navigation-back');
        ['mousedown', 'click', 'touchstart'].forEach(
            (eventType) =>
                this._externalPageBackButton.addEventListener(eventType, () => {
                    this._onBackButtonClick();
                }));
    }

    public show(localUrl: string, onHideCallback: () => void): void {
        this._onHideCallback = onHideCallback;
        if (localUrl.charAt(0) === '/') {
            localUrl = localUrl.slice(1);
        }

        document.querySelector('.external-page-iframe').classList.add('hidden');
        document.querySelector('.external-page-status').classList.remove('hidden');
        document.querySelector('.external-page-status-text').innerHTML = 'Loading...';

        setTimeout(() => {
            document.querySelector('.external-page-iframe').classList.remove('hidden');
            document.querySelector('.external-page-status').classList.add('hidden');
        }, 2000);

        document.querySelector('.external-page').classList.remove('hidden');
        if (localUrl.includes('?')) {
            document.querySelector('.external-page-iframe').setAttribute('src', localUrl + '&disableNavigationBar=1');
        } else {
            document.querySelector('.external-page-iframe').setAttribute('src', localUrl + '?disableNavigationBar=1');
        }
    }

    public hide(): void {
        document.querySelector('.external-page').classList.add('hidden');
        document.querySelector('.external-page-iframe').setAttribute('src', '');
        this._onHideCallback = null;
    }

    private _onBackButtonClick(): void {
        const onHideCallback = this._onHideCallback;
        this.hide();
        if (onHideCallback != null) {
            onHideCallback();
        }

    }

}
