import { HtmlHelpers } from './helpers';
import { CommunicationHandler, Command, WatchdogStates } from '../../infrastructure/communication/communication-handler';
import { Log } from '../../infrastructure/log';
import { NetlistChecksumHandler } from '../../infrastructure/netlist-checksum-handler';
export class PageOverlays {

    private _overlayWebSocketError: HTMLElement;
    private _overlayBusError: HTMLElement;
    private _overlayStarting: HTMLElement;
    private _overlayReload: HTMLElement;
    private _communicationHandler: CommunicationHandler = null;
    private _netlistChecksumHandler: NetlistChecksumHandler;
    private _overlayWhitePageBlocker: HTMLDivElement;
    private _imagesLoaded: boolean = false;
    private _watchdogState: WatchdogStates;
    private _overlayWarningChecksum: HTMLDivElement;
    private _overlayWarningTest: HTMLDivElement;
    private _showWarnings: boolean = true;
    private _warningHideTimeout: number = 1000 * 60; // ms
    private _hideWarningPressTimeout: number = 1000 * 5; // ms
    private _lastKnownChecksum: number = null;
    private _holdDownTimeout: NodeJS.Timeout = null;
    private _globalMouseUpEvent: (event: Event) => boolean;

    constructor(communicationHandler: CommunicationHandler, netlistChecksumHandler: NetlistChecksumHandler) {
        this._communicationHandler = communicationHandler;
        this._netlistChecksumHandler = netlistChecksumHandler;
    }
    public initialize(): void {
        this._overlayWebSocketError = document.querySelector('.overlay.overlay-websocket-error');
        this._overlayBusError = document.querySelector('.overlay.overlay-bus-error');
        this._overlayStarting = document.querySelector('.overlay.overlay-start');
        this._overlayReload = document.querySelector('.overlay.overlay-reload');
        this._overlayWhitePageBlocker = document.getElementById('overlay-white-page-blocker') as HTMLDivElement;
        this._overlayWarningChecksum = document.getElementById('checksum-warning-message') as HTMLDivElement;
        this._overlayWarningTest = document.getElementById('test-warning-message') as HTMLDivElement;

        this._setOverlayStarting(true);
        this._setOverlayWebSocketError(false);
        this._setOverlayBusError(false);
        this._setOverlayReload(false);

        this._drawOverlayWarnings();

        this._removeWhitePageBlocker();
        void this._imageLoadedNotifier();
        this._registerWarningTouchEvents();

        this._communicationHandler.addWatchdogStateChangedDelegate((state: WatchdogStates) => this._onSocketStateChanged(state));
        this._communicationHandler.addOnCommandReceivedDelegate((command: Command) => this._onCommandReceived(command));

        this._globalMouseUpEvent = (event) => {
            this._buttonUp(event);
            event.preventDefault();
            return false;
        };
    }
    private async _imageLoadedNotifier(): Promise<void> {
        await Promise.all(Array.from(document.images).map(img => {
            if (img.complete)
                return Promise.resolve(img.naturalHeight !== 0);
            return new Promise(resolve => {
                // eslint-disable-next-line no-self-assign
                // TD50: img.src = img.src; // Trigger load when image is loaded from cache.
                img.addEventListener('load', () => resolve(true));
                img.addEventListener('error', () => resolve(false));
            });
        })).then((results) => {
            if (results.every(res => res)) {
                // All images loaded correct
            }
            else {
                // One or more images is done loading got error event.
            }
            this._imagesLoaded = true;
            this._calculateOverlay();
        });
    }
    private _hideWarnings(): void {
        if (!this._showWarnings) {
            return;
        }
        this._showWarnings = false;
        this._drawOverlayWarnings();
        setTimeout(() => {
            this._showWarnings = true;
            this._drawOverlayWarnings();
        }, this._warningHideTimeout);
    }
    private _drawOverlayWarnings(): void {
        this._calculateChecksumWarning();
        this._calculateTestWaring();
    }

    private _registerWarningTouchEvents(): void {
        ['mousedown', 'touchstart'].forEach(
            (eventType) => {
                this._overlayWarningTest.addEventListener(eventType, (event: Event) => {
                    this._hideWarningsEventListener(event);
                    event.stopPropagation();
                });

                this._overlayWarningChecksum.addEventListener(eventType, (event: Event) => {
                    this._hideWarningsEventListener(event);
                    event.stopPropagation();
                });
            });
    }

    private _buttonUp(_event: Event): void {
        document.removeEventListener('mouseup', this._globalMouseUpEvent);
        if (this._holdDownTimeout != null) {
            clearTimeout(this._holdDownTimeout);
            this._holdDownTimeout = null;
            return;
        }
    }
    private _hideWarningsEventListener(event: Event): void {
        if (event.type === 'mousedown') {
            document.addEventListener('mouseup', this._globalMouseUpEvent);
        }
        this._holdDownTimeout = setTimeout(() => this._hideWarnings(), this._hideWarningPressTimeout);
        event.preventDefault();
    }
    private _removeWhitePageBlocker(): void {
        // Inline styled div that blocks white page before css is loaded.
        if (this._overlayWhitePageBlocker != null) {
            this._overlayWhitePageBlocker.remove();
        }
    }

    private _setOverlayWebSocketError(visible: boolean): void {
        HtmlHelpers.setElementVisibility(this._overlayWebSocketError, visible);
    }

    private _setOverlayBusError(visible: boolean): void {
        HtmlHelpers.setElementVisibility(this._overlayBusError, visible);
    }

    private _setOverlayStarting(visible: boolean): void {
        if (this._overlayStarting == null) {
            return;
        }

        if (visible) {
            HtmlHelpers.showElement(this._overlayStarting);
        }
        else {
            if (this._netlistChecksumHandler.hasBothChecksums()) {
                if (this._netlistChecksumHandler.isChecksumValid()) {
                    HtmlHelpers.hideElement(this._overlayStarting);
                }
                else {
                    HtmlHelpers.showElement(this._overlayStarting.querySelector('.checksum-warning'));
                    setTimeout(() => {
                        HtmlHelpers.hideElement(this._overlayStarting);
                    }, 5000);
                }
            }
            else {
                setTimeout(() => {
                    this._setOverlayStarting(false);
                }, 500);
            }
        }
    }

    private _setOverlayReload(visible: boolean): void {
        HtmlHelpers.setElementVisibility(this._overlayReload, visible);
    }

    private _calculateTestWaring(): void {
        if (this._overlayWarningTest != null) {
            const text = this._overlayWarningTest.innerText;
            if (text != null && text.trim().length > 0 && this._showWarnings) {
                HtmlHelpers.setElementVisibility(this._overlayWarningTest, true);
            }
            else {
                HtmlHelpers.setElementVisibility(this._overlayWarningTest, false);
            }
        }
    }
    private _calculateChecksumWarning(): void {
        // if new checksum during hidden warning display it anyways.
        if (this._netlistChecksumHandler.hasBothChecksums()) {
            let showWarnings = this._showWarnings;
            Log.logDebug('show warnings: ' + this._showWarnings)
            if (this._lastKnownChecksum != null && this._lastKnownChecksum !== this._netlistChecksumHandler.configurationChecksumFromWDU) {
                showWarnings = true;
                Log.logDebug('Last known checksum has changed!');
            }

            if (this._netlistChecksumHandler.isChecksumValid()) {
                this._setChecksumBusError(false);
            }
            else if (showWarnings) {
                Log.logDebug('Configuration checksum from projectinfo: ' + this._netlistChecksumHandler.configurationChecksumFromProject);
                Log.logDebug('Configuration checksum from WDU: ' + this._netlistChecksumHandler.configurationChecksumFromWDU);
                Log.logDebug('Last known checksum: ' + this._lastKnownChecksum);
                this._setChecksumBusError(true);
            } else {
                this._setChecksumBusError(false);
            }

        }
        this._lastKnownChecksum = this._netlistChecksumHandler.configurationChecksumFromWDU;
    }
    private _setChecksumBusError(visible: boolean): void {
        HtmlHelpers.setElementVisibility(this._overlayWarningChecksum, visible);
    }

    private _onSocketStateChanged(state: WatchdogStates): void {
        this._watchdogState = state;
        this._calculateOverlay();
    }

    private _calculateOverlay(): void {
        if (!this._imagesLoaded) {
            return;
        }
        switch (this._watchdogState) {
            case WatchdogStates.ok:
                this._setOverlayWebSocketError(false);
                this._setOverlayBusError(false);
                this._setOverlayStarting(false);
                break;
            case WatchdogStates.noConnectionToWdu:
                this._setOverlayWebSocketError(true);
                this._setOverlayBusError(false);
                break;
            case WatchdogStates.noConnectionToDigitalSwitching:
                this._setOverlayWebSocketError(false);
                this._setOverlayBusError(true);
                break;
        }
    }

    private _onCommandReceived(command: Command): void {
        switch (command) {
            case Command.forcePageReload:
                this._setOverlayReload(true);
                break;
            case Command.checksumReceived:
                Log.logDebug('Service Provider checksum received');
                this._calculateChecksumWarning();
                break;
            default:
                break;
        }
    }
}
