import { HtmlHelpers, NumberHelpers } from '../helpers';
import { ICommunicationHandler } from '../../../infrastructure/communication/communication-handler';
import { ValueType } from '../../../infrastructure/value-converter/value-types';
import { Component } from './component';
import { MfdStatusType, MfdStatusUpdateDataContent, StatusUpdateDataContent } from '../../../infrastructure/communication/protocol';

// eslint-disable-next-line no-shadow
enum GaugeSlicedOverlayAnimationState {
    animating = 0,
    animationDisabledDueToInvisibility = 1,
    animationDisabledEnableNextFrame = 2
}

export class GaugeSlicedOverlay extends Component {
    private _minValue: number;
    private _maxValue: number;
    private _startPosition: number;
    private _endPosition: number;
    private _counterClockwise: boolean;
    private _value: number = 0;
    private _position: number = 0;
    private _currentAnimationPosition: number = 0;
    private _startAnimationPosition: number;
    private _startAnimationTimestamp: number;
    private _disableAnimation: boolean;
    private _positionRange: number;
    private _valueRange: number;
    private _overlayImage: HTMLElement;
    private _canvasElement: HTMLCanvasElement;
    private _canvasImage: HTMLImageElement = null;
    private _animationFrameRequested: boolean = false;
    private _animationRequestFrameId: number;
    private _animationState: GaugeSlicedOverlayAnimationState = GaugeSlicedOverlayAnimationState.animationDisabledDueToInvisibility;

    constructor(element: HTMLElement, communicationHandler: ICommunicationHandler) {
        super(element, communicationHandler);
    }
    public get currentValue(): number {
        return this._value;
    }
    public get currentPosition(): number {
        return this._position;
    }
    public get startPosition(): number {
        return this._startPosition;
    }
    public get endPosition(): number {
        return this._endPosition;
    }

    public get counterClockwise(): boolean {
        return this._counterClockwise;
    }

    public allowSignalTypes(): MfdStatusType[] {
        return [MfdStatusType.statusUpdate];
    }

    public onRedraw(): void {
        this._startAnimationTimestamp = null;
        this._startAnimationPosition = this._currentAnimationPosition;

        if (this._animationFrameRequested) {
            window.cancelAnimationFrame(this._animationRequestFrameId);
        }
        this._animationRequestFrameId = window.requestAnimationFrame((timestamp) => this._drawSlicedOverlay(timestamp));
        this._animationFrameRequested = true;
    }

    public onVisibilityChanged(visible: boolean): void {
        if (visible) {
            this._canvasElement.height = parseFloat(getComputedStyle(this.element).getPropertyValue('height'));
            this._canvasElement.width = parseFloat(getComputedStyle(this.element).getPropertyValue('width'));
        }
        else if (!visible) {
            this._animationState = GaugeSlicedOverlayAnimationState.animationDisabledDueToInvisibility;
        }
    }

    public onMfdDataReceived(data: MfdStatusUpdateDataContent): void {
        this._updateValue(data.value, data.valueTypeIdentifier);
    }

    public onNmeaDataReceived(data: StatusUpdateDataContent): void {
        this._updateValue(data.value, data.valueTypeIdentifier);
    }

    protected onInitialize(): void {
        this._parseAttributes();
        this._setDomElements();
        this._addAnimationStyle();
        this._positionRange = this._endPosition - this._startPosition;
        this._valueRange = this._maxValue - this._minValue;
        this._counterClockwise = this._endPosition < this._startPosition;
        this._position = this._startPosition;

        this._canvasElement.height = parseFloat(getComputedStyle(this.element).getPropertyValue('height'));
        this._canvasElement.width = parseFloat(getComputedStyle(this.element).getPropertyValue('width'));

        window.addEventListener('resize', () => {
            this._canvasElement.height = parseFloat(getComputedStyle(this.element).getPropertyValue('height'));
            this._canvasElement.width = parseFloat(getComputedStyle(this.element).getPropertyValue('width'));
            this.refresh();
        });
    }

    private _setDomElements(): void {
        this._overlayImage = this.element.querySelector('.gauge-overlay');
        if (this._overlayImage != null) {
            HtmlHelpers.hideElement(this._overlayImage);
            this._canvasElement = document.createElement('canvas');
            this._canvasElement.height = parseFloat(getComputedStyle(this.element).getPropertyValue('height'));
            this._canvasElement.width = parseFloat(getComputedStyle(this.element).getPropertyValue('width'));
            this.element.appendChild(this._canvasElement);
            this._canvasImage = new Image();
            this._canvasImage.src = this._overlayImage.getAttribute('src');
        }
    }

    private _addAnimationStyle(): void {
        if (!this._disableAnimation) {
            this._overlayImage.classList.add('gauge-animation');
        }
    }

    private _parseAttributes(): void {
        this._minValue = parseInt(this.element.dataset.minValue, 10);
        this._maxValue = parseInt(this.element.dataset.maxValue, 10);
        this._startPosition = parseInt(this.element.dataset.startPosition, 10);
        this._endPosition = parseInt(this.element.dataset.endPosition, 10);
        this._disableAnimation = NumberHelpers.parseBooleanArgument(this.element.dataset.disableAnimation); // disable-animation
    }

    private _drawSlicedOverlay(timestamp: number): void {
        if (this._disableAnimation) {
            this._currentAnimationPosition = this._position;
        }
        else if (this._animationState !== GaugeSlicedOverlayAnimationState.animating) {
            this._currentAnimationPosition = this._position;
            if (this._animationState === GaugeSlicedOverlayAnimationState.animationDisabledEnableNextFrame) {
                this._animationState = GaugeSlicedOverlayAnimationState.animating;
            }
        }
        else {
            const diff = (this._startAnimationTimestamp == null) ? 0 : timestamp - this._startAnimationTimestamp;
            if (this._startAnimationTimestamp == null) {
                this._startAnimationTimestamp = timestamp;
            }

            const maxDelay = 500;
            const progressFactor = Math.min(diff / maxDelay, 1.0);
            const smoothFactor = (Math.pow(progressFactor, 2)) / (Math.pow(progressFactor, 2) + Math.pow(1 - progressFactor, 2));

            this._currentAnimationPosition = ((this._position - this._startAnimationPosition) * smoothFactor) + this._startAnimationPosition;
        }
        const cx = this._canvasElement.width / 2;
        const cy = this._canvasElement.height / 2;
        const radius = this._canvasElement.width / 2;
        const context = this._canvasElement.getContext('2d');
        context.clearRect(0, 0, this._canvasElement.width, this._canvasElement.height);
        context.save();
        const degreeCorrection = -90;
        const startAngle = (Math.PI / 180) * (this._startPosition + degreeCorrection);
        const endAngle = (Math.PI / 180) * (this._currentAnimationPosition + degreeCorrection);
        context.beginPath();
        context.moveTo(cx, cy);
        if (this._counterClockwise) {
            context.arc(cx, cy, radius, endAngle, startAngle);
        } else {
            context.arc(cx, cy, radius, startAngle, endAngle);
        }
        context.lineTo(cx, cy);
        context.clip();
        context.drawImage(this._canvasImage, 0, 0, this._canvasElement.width, this._canvasElement.height);
        context.restore();

        if (Math.abs(this._currentAnimationPosition - this._position) < 0.001) {
            this._currentAnimationPosition = this._position;
            this._animationFrameRequested = false;
            this._animationRequestFrameId = 0;
        }
        else {
            this._animationFrameRequested = true;
            this._animationRequestFrameId = window.requestAnimationFrame(this._drawSlicedOverlay.bind(this));
        }
    }

    private _updateValue(value: number, _valueTypeIdentifier: ValueType): void {
        if (this._animationState !== GaugeSlicedOverlayAnimationState.animating) {
            this._animationState = GaugeSlicedOverlayAnimationState.animationDisabledEnableNextFrame;
        }
        this._value = NumberHelpers.clamp(value, this._minValue, this._maxValue);
        const valueRatio = (this._value - this._minValue) / (this._valueRange);
        this._position = this._positionRange * valueRatio + this._startPosition;
        this.refresh();
    }
}
