import { HtmlHelpers } from './helpers';

export class ViewPort {
    public id: string;
    public aspectRatio: number;
    public screenSaverPageId: string;
    private _element: HTMLElement;
    private _visible: boolean = false;
    constructor(pageElement: HTMLElement, id: string, aspectRatio: number, screenSaverPageId: string) {
        this._element = pageElement;
        this.id = id;
        this.aspectRatio = aspectRatio;
        this.screenSaverPageId = screenSaverPageId;
    }

    get isVisible(): boolean {
        return this._visible;
    }

    public setVisibility(visible: boolean): void {
        HtmlHelpers.setElementVisibility(this._element, visible);
        if (this._visible !== visible) {
            this._visible = visible;
        }
    }


}
