import { HtmlHelpers, NumberHelpers } from '../helpers';
import { ICommunicationHandler } from '../../../infrastructure/communication/communication-handler';
import { Component } from './component';
import { MfdStatusType, MfdStatusUpdateDataContent, StatusUpdateDataContent } from '../../../infrastructure/communication/protocol';

export class LevelGauge extends Component {
    private _currentLevelRatio: number;
    private _currentValue: number = 0;
    private _minValue: number;
    private _maxValue: number;
    private _minHeightRatio: number;
    private _maxHeightRatio: number;
    private _thresholdLowValue: number;
    private _thresholdHighValue: number;
    private _levelDiv: HTMLElement;
    private _levelImage: HTMLElement;
    private _levelImageLowThreshold: HTMLElement;
    private _levelImageHighThreshold: HTMLElement;
    private _levelTrackerImage: HTMLElement;
    private _levelTrackerImageLowThreshold: HTMLElement;
    private _levelTrackerImageHighThreshold: HTMLElement;
    private _elementHeight: number;
    private _elementWidth: number;
    private _trackerHeight: number;
    private _trackerWidth: number;
    private _trackerLowThresholdHeight: number;
    private _trackerLowThresholdWidth: number;
    private _trackerHighThresholdHeight: number;
    private _trackerHighThresholdWidth: number;
    private _disableAnimation: boolean;
    private _removeNoTransitionOnNextRedraw: boolean; // Used for disabling animation during visibility change
    private _valueReceived: boolean = false; // Used for disabling animation if not value has been received yet

    constructor(element: HTMLElement, communicationHandler: ICommunicationHandler) {
        super(element, communicationHandler);
    }

    public get currentValue(): number {
        return this._currentValue;
    }
    public get currentLevelRatio(): number {
        return this._currentLevelRatio;
    }

    public get minHeigtRatio(): number {
        return this._minHeightRatio;
    }
    public get maxHeigtRatio(): number {
        return this._maxHeightRatio;
    }

    public allowSignalTypes(): MfdStatusType[] {
        return [MfdStatusType.statusUpdate];
    }

    public onRedraw(): void {
        if (this._thresholdLowValue != null && this._thresholdLowValue >= this._currentValue) {
            HtmlHelpers.hideElement(this._levelImage);
            HtmlHelpers.showElement(this._levelImageLowThreshold);
            HtmlHelpers.hideElement(this._levelImageHighThreshold);
            HtmlHelpers.hideElement(this._levelTrackerImage);
            HtmlHelpers.showElement(this._levelTrackerImageLowThreshold);
            HtmlHelpers.hideElement(this._levelTrackerImageHighThreshold);
        }
        else if (this._thresholdHighValue != null && this._thresholdHighValue <= this._currentValue) {
            HtmlHelpers.hideElement(this._levelImage);
            HtmlHelpers.hideElement(this._levelImageLowThreshold);
            HtmlHelpers.showElement(this._levelImageHighThreshold);
            HtmlHelpers.hideElement(this._levelTrackerImage);
            HtmlHelpers.hideElement(this._levelTrackerImageLowThreshold);
            HtmlHelpers.showElement(this._levelTrackerImageHighThreshold);
        }
        else {
            HtmlHelpers.showElement(this._levelImage);
            HtmlHelpers.hideElement(this._levelImageLowThreshold);
            HtmlHelpers.hideElement(this._levelImageHighThreshold);
            HtmlHelpers.showElement(this._levelTrackerImage);
            HtmlHelpers.hideElement(this._levelTrackerImageLowThreshold);
            HtmlHelpers.hideElement(this._levelTrackerImageHighThreshold);
        }
        this._resolveCurrentHeightsAndWidths();

        const top = this._elementHeight - (this._currentLevelRatio - this._minHeightRatio) * this._elementHeight;
        if (this._levelDiv != null) {
            this._levelDiv.style.clip = `rect(${top}px, ${this._elementWidth}px, ${this._elementHeight}px, 0px)`;
        }

        // vertical positioning of handle
        if (this._levelTrackerImage != null) {
            const trackerBottom = this._currentLevelRatio * this._elementHeight - this._trackerHeight / 2;
            const trackerBottomPx = `${trackerBottom}px`;
            const trackerLeftPx = `${this._elementWidth / 2 - this._trackerWidth / 2}px`;
            this._levelTrackerImage.style.bottom = trackerBottomPx;
            this._levelTrackerImage.style.left = trackerLeftPx;
        }

        // vertical positioning of handle
        if (this._levelTrackerImageLowThreshold != null) {
            const trackerBottom = this._currentLevelRatio * this._elementHeight - this._trackerLowThresholdHeight / 2;
            const trackerBottomPx = `${trackerBottom}px`;
            const trackerLeftPx = `${this._elementWidth / 2 - this._trackerLowThresholdWidth / 2}px`;
            this._levelTrackerImageLowThreshold.style.bottom = trackerBottomPx;
            this._levelTrackerImageLowThreshold.style.left = trackerLeftPx;
        }

        // vertical positioning of handle
        if (this._levelTrackerImageHighThreshold != null) {
            const trackerBottom = this._currentLevelRatio * this._elementHeight - this._trackerHighThresholdHeight / 2;
            const trackerBottomPx = `${trackerBottom}px`;
            const trackerLeftPx = `${this._elementWidth / 2 - this._trackerHighThresholdWidth / 2}px`;
            this._levelTrackerImageHighThreshold.style.bottom = trackerBottomPx;
            this._levelTrackerImageHighThreshold.style.left = trackerLeftPx;
        }

        // Remove notransition class separately so that the notransition is still active during the current redraw
        if (this._removeNoTransitionOnNextRedraw && this._valueReceived) {
            setTimeout(() => {
                this.element.classList.remove('notransition');
            }, 0);
            this._removeNoTransitionOnNextRedraw = false;
        }
    }

    public onVisibilityChanged(visible: boolean): void {
        if (!visible) {
            this.element.classList.add('notransition');
        }
        else {
            this._removeNoTransitionOnNextRedraw = true;
        }
    }

    public onMfdDataReceived(data: MfdStatusUpdateDataContent): void {
        this._calculateValues(data.value);
        this.refresh();  // Not good?
    }
    public onNmeaDataReceived(data: StatusUpdateDataContent): void {
        this._calculateValues(data.value);
        this.refresh();
    }

    protected onInitialize(): void {
        this._parseHtmlAttributes();
        this.setDomElements();
        this._addAnimationStyle();
        this._resolveCurrentHeightsAndWidths();
        window.addEventListener('resize', () => this.refresh());
    }

    private setDomElements(): void {
        this._levelDiv = this.element.querySelector('.levelgauge-level');
        this._levelImage = this.element.querySelector('.levelgauge-levelimage');
        this._levelImageLowThreshold = this.element.querySelector('.levelgauge-levelimage-low-threshold');
        this._levelImageHighThreshold = this.element.querySelector('.levelgauge-levelimage-high-threshold');
        this._levelTrackerImage = this.element.querySelector('.levelgauge-leveltrackerimage');
        this._levelTrackerImageLowThreshold = this.element.querySelector('.levelgauge-leveltrackerimage-low-threshold');
        this._levelTrackerImageHighThreshold = this.element.querySelector('.levelgauge-leveltrackerimage-high-threshold');
    }

    private _addAnimationStyle(): void {
        if (!this._disableAnimation) {
            this._levelDiv?.classList.add('levelgauge-animation');
            this._levelImage?.classList.add('levelgauge-leveltracker-animation');
            this._levelTrackerImage?.classList.add('levelgauge-leveltracker-animation');
            this._levelTrackerImageHighThreshold?.classList.add('levelgauge-leveltracker-animation');
            this._levelTrackerImageLowThreshold?.classList.add('levelgauge-leveltracker-animation');
            this._levelImageHighThreshold?.classList.add('levelgauge-leveltracker-animation');
            this._levelImageLowThreshold?.classList.add('levelgauge-leveltracker-animation');
        }
    }

    private _parseHtmlAttributes(): void {
        this._minValue = parseInt(this.element.dataset.minValue, 10); // min-value
        this._maxValue = parseInt(this.element.dataset.maxValue, 10); // max-value
        this._minHeightRatio = parseInt(this.element.dataset.minHeight, 10) * 0.01; // min-height
        this._maxHeightRatio = parseInt(this.element.dataset.maxHeight, 10) * 0.01; // max-height
        if (this._maxHeightRatio < this._minHeightRatio) {
            const mem = this._minHeightRatio;
            this._minHeightRatio = this._maxHeightRatio;
            this._maxHeightRatio = mem;
        }
        this._currentLevelRatio = this._minHeightRatio;
        this._thresholdLowValue = parseInt(this.element.dataset.thresholdLowValue, 10); // threshold-low-value
        this._thresholdHighValue = parseInt(this.element.dataset.thresholdHighValue, 10); // threshold-high-value
        this._disableAnimation = NumberHelpers.parseBooleanArgument(this.element.dataset.disableAnimation); // disable-animation
    }

    private _resolveCurrentHeightsAndWidths(): void {

        this._elementHeight = parseFloat(getComputedStyle(this.element).getPropertyValue('height'));
        this._elementWidth = parseFloat(getComputedStyle(this.element).getPropertyValue('width'));

        if (this._levelTrackerImage != null) {
            this._trackerHeight = parseFloat(getComputedStyle(this._levelTrackerImage).getPropertyValue('height'));
            this._trackerWidth = parseFloat(getComputedStyle(this._levelTrackerImage).getPropertyValue('width'));
        }
        // vertical positioning of handle
        if (this._levelTrackerImageLowThreshold != null) {

            this._trackerLowThresholdHeight = parseFloat(getComputedStyle(this._levelTrackerImageLowThreshold).getPropertyValue('height'));
            this._trackerLowThresholdWidth = parseFloat(getComputedStyle(this._levelTrackerImageLowThreshold).getPropertyValue('width'));
        }
        if (this._levelTrackerImageHighThreshold != null) {
            this._trackerHighThresholdHeight = parseFloat(getComputedStyle(this._levelTrackerImageHighThreshold).getPropertyValue('height'));
            this._trackerHighThresholdWidth = parseFloat(getComputedStyle(this._levelTrackerImageHighThreshold).getPropertyValue('width'));
        }
    }

    private _calculateValues(rawValue: number): void {
        this._valueReceived = true;

        let maxValue = this._maxValue;
        let minValue = this._minValue;
        if (this._maxValue < this._minValue) {
            maxValue = this._minValue;
            minValue = this._maxValue;
        }

        let value = NumberHelpers.clamp(rawValue, minValue, maxValue);

        this._currentValue = value;  // Used by tresholds, do not invert if max < min
        if (this._maxValue < this._minValue) {
            value = maxValue - value;
        }

        const heightRange = this._maxHeightRatio - this._minHeightRatio;
        const valueCoefficient = (value - minValue) / Math.abs(this._maxValue - this._minValue);
        this._currentLevelRatio = valueCoefficient * heightRange + this._minHeightRatio;
    }

}
