import { UrlHelper } from '../../infrastructure/url-helper';
import { AppSelectorLinkTimer } from '../app-selector/app-selector-link-timer';
import { HtmlHelpers } from './helpers';
import { PageNavigator } from './page-navigator';

export class AppSelectorLinkTimerDialog {
    private _pageNavigator: PageNavigator;
    private _dialogElement: HTMLElement;
    private _navigateButton: HTMLElement;
    private _cancelButton: HTMLElement;
    private _returnToAppSelector: boolean;

    constructor(pageNavigator: PageNavigator) {
        this._pageNavigator = pageNavigator;
        this._dialogElement = document.querySelector('.app-selector-link-timer-dialog');
        this._navigateButton = document.querySelector('.app-selector-link-timer-dialog-button-navigate');
        ['mousedown', 'click', 'touchstart'].forEach(
            (eventType) =>
                this._navigateButton.addEventListener(eventType, (_event: Event) => {
                    this.navigate();
                }));
        this._cancelButton = document.querySelector('.app-selector-link-timer-dialog-button-cancel');
        ['mousedown', 'click', 'touchstart'].forEach(
            (eventType) =>
                this._cancelButton.addEventListener(eventType, (_event: Event) => {
                    this.hideDialog();
                }));

        const returnUrlQueryParam = UrlHelper.getUrlParamValue('returnUrl');
        this._returnToAppSelector = (returnUrlQueryParam != null && returnUrlQueryParam !== '');

        if (this._returnToAppSelector) {
            this._navigateButton.innerHTML = 'Application Selector';
        }
        else {
            this._navigateButton.innerHTML = 'Settings';
        }
    }

    public initialize(): void {
        this._initializeAppSelectorLinkTimer();
    }

    public showDialog(): void {
        HtmlHelpers.showElement(this._dialogElement);
    }

    public hideDialog(): void {
        HtmlHelpers.hideElement(this._dialogElement);
    }

    public navigate(): void {

        if (this._returnToAppSelector) {
            document.location = 'index.html?forceShowList=1';
        } else {
            this._pageNavigator.setActiveExternalPage('settings-web/#')
        }

    }

    private _initializeAppSelectorLinkTimer(): void {
        new AppSelectorLinkTimer(() => {

            this.showDialog();
        });
    }
}