export class UrlHelper {
    public static cleanUrl(url: string): string {
        if (!url.includes('?')) {
            return url;
        }

        const urlParts = url.split('?');
        let retValue = url;
        urlParts.forEach((urlPart, i) => {
            if (i === 0) {
                retValue = urlPart;
                retValue += '?';
            }
            else {
                retValue += urlPart;
                retValue += '&';
            }
        });
        retValue = retValue.substring(0, retValue.length -1)
        return retValue;
    }

    public static getUrlParamValue(param: string): string {
        const url = new URL(UrlHelper.cleanUrl(window.location.href)).searchParams;
        const paramValue = url.get(param);
        if (paramValue == null) {
            return '';
        }
        return paramValue;
    }
    public static removeUrlParameter(param: string): void {
        const url = this.removeUrlParameterHref(param, window.location.href);
        window.history.pushState('', '', url.toString());
    }
    public static removeUrlParameterHref(param: string, href: string): string {
        const url = new URL(UrlHelper.cleanUrl(href));
        url.searchParams.delete(param);
        return url.toString();
    }
    public static getPathWithReturnUrl(path: string, extraReturnUrlParams: Map<string, string>): string {
        const url = new URL(window.location.href);
        extraReturnUrlParams.forEach((value, key) => {
            url.searchParams.append(key, value);
        });
        return path + '?returnUrl=' + encodeURIComponent(url.toString());
    }

    public static createUrlWithReturnParameter(navigationUrl: string, pageId: string = null): string {

        if (navigationUrl === '') {
            return '';
        }
        const hostUrl = 'http://' + window.location.host;
        // const hostUrl = 'http://172.16.16.0'; //Use when debugging locally
        let settingsUrl = `${hostUrl}/${navigationUrl}`;
        const urlHref = UrlHelper.cleanUrl(window.location.href);
        const appUrl = new URL(urlHref);
        const returnUrls = appUrl.searchParams.getAll('returnUrl');

        // clear all returnUrls and add them in order
        appUrl.searchParams.delete('returnUrl');

        if (pageId != null) {
            appUrl.searchParams.append('pageId', pageId);
        }
        const encodedAppUrl = encodeURIComponent(appUrl.toString())

        const urlAppends = [];

        returnUrls.forEach(url => {
            urlAppends.push('returnUrl=' + encodeURIComponent(url));
        })
        urlAppends.push('returnUrl=' + encodedAppUrl);

        urlAppends.forEach((append, i) => {
            if (i === 0)
                settingsUrl += '?'
            else
                settingsUrl += '&'
            settingsUrl += append;
        })
        return settingsUrl;

    }
}