import { WebSocketManager } from './web-socket-manager';
import { ClientControlCommands, MessageTypeCode, MfdStatusType, MfdStatusUpdateDataContent, Protocol, StatusUpdateDataContent, SystemCommandTypes, WebsocketMessage } from './protocol';
import { Log, } from '../log';
import { Timer } from '../timer';
import { WebSocketContentDecoder } from './web-socket-content-decoder';
import { VirtualSignalExpr } from '../signals/virtual-signal-expr';
import { ExpressionType } from '../signals/expression-type';

// eslint-disable-next-line no-shadow
export enum Command {
    forcePageReload = 0,
    settingsUpdated = 1,
    wduInfoReceived = 3,
    checksumReceived = 4,
    applicationSelectionListReceived = 5,
}

// eslint-disable-next-line no-shadow
export enum WatchdogStates {
    notSet = 0,
    ok = 1,
    noConnectionToDigitalSwitching = 2,
    noConnectionToWdu = 3
}

export type OnWatchdogStateChangedDelegate = (state: WatchdogStates) => void;
export type OnNmeaValueReceivedDelegate = (value: StatusUpdateDataContent) => void;
export type OnMfdValueReceivedDelegate = (value: MfdStatusUpdateDataContent) => void;
export type OnCommandReceivedDelegate = (command: Command, extradata?: object) => void;

export interface ICommunicationHandler {
    configurationChecksumFromWDU: number;
    addOnNmeaValueReceivedDelegate(_signalId: number, _delegate: OnNmeaValueReceivedDelegate): void;
    addOnMfdValueReceivedDelegate(_signalId: number, _delegate: OnMfdValueReceivedDelegate): void;
    addOnCommandReceivedDelegate(delegate: OnCommandReceivedDelegate): void;
    removeOnWathdogStateChangedDelegate(delegate: OnWatchdogStateChangedDelegate): void;
    send(_command: any): void;
    getProtocolVersion(): number[]; // TODO Semver
}

export class CommunicationHandler implements ICommunicationHandler {

    public configurationChecksumFromWDU: number = null;
    public watchdogState: WatchdogStates = WatchdogStates.notSet;
    private _webSocketManager: WebSocketManager = null;
    private _nmeaValueReceivedDelegatesMap: Map<number, OnNmeaValueReceivedDelegate[]> = new Map<number, OnNmeaValueReceivedDelegate[]>();
    private _mdfValueReceivedDelegatesMap: Map<number, OnMfdValueReceivedDelegate[]> = new Map<number, OnMfdValueReceivedDelegate[]>();
    private _onWatchdogStateChangesDelegates: OnWatchdogStateChangedDelegate[] = [];
    private _commandReceivedDelegates: OnCommandReceivedDelegate[] = [];
    private _digitalSwitchingHeartbeatTimestamp: number = 0;
    private _lastReceivedMessageTimestamp: number = 0;
    private _wduSendsHeartbeat: boolean;
    private _oldHeartbeatDurationDivider: number = 0;
    private _watchdogDuration: number = 2000;
    private _protocolVersion: number[] = [1, 0, 0];
    private _wduInfoReceived: boolean = false;
    private _contentDecoder: WebSocketContentDecoder
    private _receivedNmeaDataMap: Map<number, StatusUpdateDataContent> = new Map<number, StatusUpdateDataContent>();
    private _receivedMfdDataMap: Map<number, MfdStatusUpdateDataContent> = new Map<number, MfdStatusUpdateDataContent>();

    constructor(webSocketManager: WebSocketManager, contentDecoder: WebSocketContentDecoder) {
        this._webSocketManager = webSocketManager;
        this._contentDecoder = contentDecoder;
        this._webSocketManager.addOnDataReceivedDelegate((data: any) => this._onMessageReceived(data));
        this._lastReceivedMessageTimestamp = Timer.timestamp();
        setTimeout(() => this._watchdogLoop(), this._watchdogDuration * 2);
    }
    public getProtocolVersion(): number[] {
        return this._protocolVersion;
    }
    public addOnNmeaValueReceivedDelegate(signalId: number, delegate: OnNmeaValueReceivedDelegate): void {
        let delegates: OnNmeaValueReceivedDelegate[] = [];
        if (this._nmeaValueReceivedDelegatesMap.has(signalId)) {
            delegates = this._nmeaValueReceivedDelegatesMap.get(signalId);
        } else {
            this._nmeaValueReceivedDelegatesMap.set(signalId, delegates);
        }
        delegates.push(delegate);
    }
    public addOnMfdValueReceivedDelegate(signalId: number, delegate: OnMfdValueReceivedDelegate): void {
        let delegates: OnMfdValueReceivedDelegate[] = [];
        if (this._mdfValueReceivedDelegatesMap.has(signalId)) {
            delegates = this._mdfValueReceivedDelegatesMap.get(signalId);
        } else {
            this._mdfValueReceivedDelegatesMap.set(signalId, delegates);
        }
        delegates.push(delegate);
    }
    public addOnCommandReceivedDelegate(delegate: OnCommandReceivedDelegate): void {
        this._commandReceivedDelegates.push(delegate);
    }

    public addWatchdogStateChangedDelegate(delegate: OnWatchdogStateChangedDelegate): void {
        this._onWatchdogStateChangesDelegates.push(delegate);
        delegate(this.watchdogState)
    }

    public removeOnWathdogStateChangedDelegate(delegate: OnWatchdogStateChangedDelegate): void {
        for (let i = 0; i < this._onWatchdogStateChangesDelegates.length; i++) {
            if (this._onWatchdogStateChangesDelegates[i] === delegate) {
                this._onWatchdogStateChangesDelegates.splice(i, 1);
                return;
            }
        }
    }

    public send(command: any): void {
        this._webSocketManager.sendJson(command);
    }

    private _notifyMdfValueSubscribers(data: MfdStatusUpdateDataContent): void {
        // to avoid multiple calls to the same data, only notify if the data has changed
        if (!this._isNewMfdData(data)) {
            return;
        }
        const delegates = this._mdfValueReceivedDelegatesMap.get(data.signalId);
        if (delegates == null) {
            return;
        }
        for (const delegate of delegates) {
            delegate(data);
        }
    }

    private _notifyNmeaValueSubscribers(data: StatusUpdateDataContent): void {
        // to avoid multiple calls to the same data, only notify if the data has changed
        if (!this._isNewNmeaData(data)) {
            return;
        }
        const delegates = this._nmeaValueReceivedDelegatesMap.get(data.signalId);
        if (delegates == null) {
            return;
        }
        for (const delegate of delegates) {
            delegate(data);
        }
    }

    private _isNewMfdData(data: MfdStatusUpdateDataContent): boolean {
        if (this._receivedMfdDataMap.has(data.signalId)) {
            const oldData = this._receivedMfdDataMap.get(data.signalId);
            if (oldData.equals(data)) {
                return false;
            }
        }
        this._receivedMfdDataMap.set(data.signalId, data);
        return true;
    }

    private _isNewNmeaData(data: StatusUpdateDataContent): boolean {
        if (this._receivedNmeaDataMap.has(data.signalId)) {
            const oldData = this._receivedNmeaDataMap.get(data.signalId);
            if (oldData.equals(data)) {
                return false;
            }
        }
        this._receivedNmeaDataMap.set(data.signalId, data);
        return true;
    }

    private _onMessageReceived(message: WebsocketMessage): void {
        this._lastReceivedMessageTimestamp = Timer.timestamp();
        switch (message.messagetype) {
            case MessageTypeCode.mfdStatus:
                // message.id = message.data[0] | (message.data[1] << 8); // Why is this done
                this._propagateMfdMessage(this._contentDecoder.decodeMFDChannelItem(message))

                break;
            case MessageTypeCode.nmeaMsg:
                // message.id = message.data[0] | (message.data[1] << 8); // Why is this done
                this._propagateNmeaMessage(this._contentDecoder.decodeN2KDataItem(message))
                break;
            case MessageTypeCode.systemCmd:

                if (message.messagecmd === SystemCommandTypes.wduInfo) {
                    // WDU Info.
                    /*				WDU Protocol Version MSB	0x00-0xFF
                                    WDU Protocol Version LSB	0x00-0xFF
                                    WDU Application Firmware Version MSB	0x00-0xFF
                                    WDU  Application Firmware Version MSB	0x00-0xFF
                                    WDU BaseImage Version MSB	0x00-0xFF
                                    WDU BaseImage Version LSB	0x00-0xFF
                                    WDU Model Nr	0x00000000-0xFFFFFFFF
                                    WDU Serial Nr	0x00000000-0xFFFFFFFF
                                    NMEA 2000 Source Address	0-253*/

                    this._wduSendsHeartbeat = (message.data[0] > 1 || (message.data[0] === 1 && message.data[1] >= 2));
                    this._protocolVersion[0] = message.data[0];
                    this._protocolVersion[1] = message.data[1];
                    this._wduInfoReceived = true;
                    Log.logVerbose(`WDU Info Received. Enable WDU heartbeat set to ${this._wduSendsHeartbeat}`);
                    for (const delegate of this._commandReceivedDelegates) {
                        delegate(Command.wduInfoReceived);
                    }

                } else if (message.messagecmd === SystemCommandTypes.serviceProviderHeartbeat) { // 0x00 - Service Provider HeartBeat
                    this.configurationChecksumFromWDU = (message.data[1] | (message.data[2] << 8) | (message.data[3] << 16) | (message.data[4] << 24)) >>> 0;
                    this._digitalSwitchingHeartbeatTimestamp = Timer.timestamp();
                    if (this.configurationChecksumFromWDU !== 0) { // When 0, no heartbet from WDU has not beed received from master. => Do not check watchdog yet since it will cause a checksum missmatch
                        this._checkWatchdog();
                    }
                    for (const delegate of this._commandReceivedDelegates) {
                        delegate(Command.checksumReceived);
                    }
                    Log.logVerbose('Service provider heartbeat received');
                } else if (message.messagecmd === SystemCommandTypes.wduHeartbeat) { // 0x05 - WDU Host Heartbeat
                    Log.logDebug('Received wdu heart beat.')
                    this.send(Protocol.receivedWduHeartbetAckCommand());
                } else if (message.messagecmd === SystemCommandTypes.wduSettingsUpdated) {
                    for (const delegate of this._commandReceivedDelegates) {
                        delegate(Command.settingsUpdated);
                    }
                } else if (message.messagecmd === SystemCommandTypes.applicationSelection) {
                    for (const delegate of this._commandReceivedDelegates) {
                        delegate(Command.applicationSelectionListReceived, message.extradata);
                    }
                }
                break;
            case MessageTypeCode.clientControlCommand:
                if (message.messagecmd === ClientControlCommands.forceReload) {
                    for (const delegate of this._commandReceivedDelegates) {
                        delegate(Command.forcePageReload);
                    }
                    // TODO: Move to delegate?
                    setTimeout(() => {
                        window.location.reload();
                    }, 5000);
                }
                break;
        }
    }

    private _propagateMfdMessage(data: MfdStatusUpdateDataContent): void {
        if (data == null) {
            return;
        }
        this._notifyMdfValueSubscribers(data);
        const virtualSignals = this._contentDecoder.getVirtualSignals(data.signalId);
        for (const virtualSignal of virtualSignals) {
            const vData = new MfdStatusUpdateDataContent();
            // ternary if statement to force service unavailability if the source signal is unavailable.
            vData.unavailable = data.unavailable ? true : this._evaluate(data.value, virtualSignal.unAvailableExpression);
            vData.onOffStatus = this._evaluate(data.value, virtualSignal.statusExpression);
            vData.signalId = virtualSignal.signalId;
            vData.type = MfdStatusType.momentary;
            this._notifyMdfValueSubscribers(vData);
        }
    }

    private _propagateNmeaMessage(data: StatusUpdateDataContent): void {
        if (data == null) {
            return;
        }
        this._notifyNmeaValueSubscribers(data);
        const virtualSignals = this._contentDecoder.getVirtualSignals(data.signalId);
        for (const virtualSignal of virtualSignals) {
            const vData = new MfdStatusUpdateDataContent();
            // ternary if statement to force service unavailability if the source signal is unavailable.
            vData.unavailable = data.unavailable ? true : this._evaluate(data.value, virtualSignal.unAvailableExpression);
            vData.onOffStatus = this._evaluate(data.value, virtualSignal.statusExpression);
            vData.type = MfdStatusType.momentary;
            vData.signalId = virtualSignal.signalId;
            this._notifyMdfValueSubscribers(vData);
        }
    }

    private _evaluate(signalValue: number, expression: VirtualSignalExpr): boolean {
        switch (expression.expressionType) {
            case ExpressionType.disabled:
                return false;
            case ExpressionType.equal:
                return signalValue === expression.valueA;
            case ExpressionType.notEqual:
                return signalValue !== expression.valueA;
            case ExpressionType.greaterThan:
                return signalValue > expression.valueA;
            case ExpressionType.lessThan:
                return signalValue < expression.valueA;
            case ExpressionType.greaterThanOrEqual:
                return signalValue >= expression.valueA;
            case ExpressionType.lessThanOrEqual:
                return signalValue <= expression.valueA
            case ExpressionType.between:
                return signalValue > expression.valueA && signalValue < expression.valueB;
            case ExpressionType.notBetween:
                return !(signalValue > expression.valueA && signalValue < expression.valueB);
        }
    }

    private _onWatchdogStateChanged(state: WatchdogStates): void {
        if (this.watchdogState !== state) {
            Log.logInfo(`Watchdog state changed to: ${state}`);
            this.watchdogState = state;
            for (const delegate of this._onWatchdogStateChangesDelegates) {
                delegate(state);
            }
            this._requestWduInfo();
        }
    }

    private _requestWduInfo(): void {
        Log.logVerbose('Request WDU Info');
        this.send(Protocol.requestWduInfoCommand());
    }

    private _checkWatchdog(): number {

        let nextDelay = this._watchdogDuration;
        this._oldHeartbeatDurationDivider++;
        if (Timer.elapsedSeconds(this._lastReceivedMessageTimestamp) > 10) {
            nextDelay = 9000;
            this._onWatchdogStateChanged(WatchdogStates.noConnectionToWdu);
            this._webSocketManager.connect();
            Log.logInfo('No heartbeat from WDU received! Recreate websocket');
        } else if (this.watchdogState === WatchdogStates.noConnectionToWdu) {
            this._onWatchdogStateChanged(WatchdogStates.noConnectionToDigitalSwitching);
        }

        if (!this._wduSendsHeartbeat && this._oldHeartbeatDurationDivider > 1 && Timer.elapsedSeconds(this._lastReceivedMessageTimestamp) > 4 || !this._wduInfoReceived) { // Old WDU firmware without wdu heartbeat need this handling
            Log.logVerbose('Send heartbeat for older wdu firmwares');
            this._requestWduInfo();
            this._oldHeartbeatDurationDivider = 0;
        }

        if (this.watchdogState !== WatchdogStates.noConnectionToWdu) {
            if (Timer.elapsedSeconds(this._digitalSwitchingHeartbeatTimestamp) > 12) {
                this._onWatchdogStateChanged(WatchdogStates.noConnectionToDigitalSwitching);
            } else {
                this._onWatchdogStateChanged(WatchdogStates.ok);
            }
        }
        return nextDelay;

    }

    private _watchdogLoop(): void {
        const nextDelay = this._checkWatchdog()
        setTimeout(() => this._watchdogLoop(), nextDelay);

    }
}


