import { Component } from './component';
import { HtmlHelpers } from '../helpers';
import { PageNavigator } from '../page-navigator';
import { Layer } from '../layer';
import { LayerActionEnum } from '../layer-action-enum';
import { ICommunicationHandler } from '../../../infrastructure/communication/communication-handler';
import { MfdStatusType, Protocol } from '../../../infrastructure/communication/protocol';
import { SignalActionEnum } from '../signal-action-enum';

export class LayerAction {
    public static fromJson(json: any): LayerAction {
        const action = json.s as LayerActionEnum;
        return new LayerAction(json.l, action);
    }

    public layerId: string;
    public action: LayerActionEnum = LayerActionEnum.doNothing;
    constructor(layerId: string, action: LayerActionEnum) {
        this.layerId = layerId;
        this.action = action;
    }
}

export class NavigationButton extends Component {
    private _destinationPageId: string;
    private _destinationSystemPage: string;
    private _usesPageNavigation: boolean;
    private _pressed: boolean;
    private _requireHoldDownButtonSeconds: number;
    private _holdDownTimeout: NodeJS.Timeout = null;
    private _layerActions: LayerAction[] = null;
    private _signalAction: SignalActionEnum = null;
    private _globalMouseUpEvent: (event: Event) => boolean;
    private _elementImagePressed: HTMLElement;
    private _pageNavigator: PageNavigator;

    constructor(element: HTMLElement, communicationHandler: ICommunicationHandler, pageNavigator: PageNavigator) {
        super(element, communicationHandler);
        this._pageNavigator = pageNavigator;
    }

    public onRedraw(): void {
        HtmlHelpers.setElementVisibility(this._elementImagePressed, this._pressed);
    }

    public allowSignalTypes(): MfdStatusType[] {
        return [];
    }

    protected onInitialize(): void {
        this._elementImagePressed = this.element.querySelector('.navigationbutton-imagepressed');
        this._destinationSystemPage = this.element.dataset.destinationSystemPage; // destination-system-page
        if (this._destinationSystemPage !== undefined) {
            this._usesPageNavigation = false;
        }
        else {
            this._usesPageNavigation = true;
            this._destinationPageId = this.element.dataset.destinationPageId; // destination-page-id
        }
        this._requireHoldDownButtonSeconds = parseInt(this.element.dataset.requireHoldDownButtonSeconds, 10);
        // require-hold-down-button-seconds

        if (this._requireHoldDownButtonSeconds == null) {
            this._requireHoldDownButtonSeconds = 0;
        }
        if (this.element.dataset.layerActions) {
            const layerActions = JSON.parse(this.element.dataset.layerActions);
            if (Array.isArray(layerActions)) {

                this._layerActions = layerActions.map((x: any) => LayerAction.fromJson(x));
            }
        }

        if (this.element.dataset.signalAction) {
            this._signalAction = Number.parseInt(this.element.dataset.signalAction) as SignalActionEnum;
        }

        this._globalMouseUpEvent = (event) => {
            this.buttonUp(event);
            event.preventDefault();
            return false;
        };

        this.registerEvents();

    }

    private registerEvents(): void {
        ['mousedown', 'touchstart'].forEach(
            (eventType) => this.element.addEventListener(eventType, (event: Event) => {
                this.buttonDown(event);
                event.preventDefault();
                event.stopPropagation();
                return false;
            }));

        ['mouseup', 'touchend', 'touchcancel'].forEach(
            (eventType) => this.element.addEventListener(eventType, (event: Event) => {
                this.buttonUp(event);
                event.preventDefault();
                return false;
            }));
    }

    private buttonDown(event: Event): void {
        if (this.isUnavailable) {
            return; // Not allowed to press.
        }

        this._pressed = true;

        if (event.type === 'mousedown') {
            document.addEventListener('mouseup', this._globalMouseUpEvent);
        }

        this.refresh();

        if (this._requireHoldDownButtonSeconds > 0) {
            // TODO arrow function instead of bind?
            // eslint-disable-next-line @typescript-eslint/no-implied-eval
            this._holdDownTimeout = setTimeout(this.triggerClickAfterHoldDown.bind(this), 1000 * this._requireHoldDownButtonSeconds);
        }
    }

    private buttonUp(_event: Event): void {
        document.removeEventListener('mouseup', this._globalMouseUpEvent);

        if (this._holdDownTimeout != null) {
            clearTimeout(this._holdDownTimeout);
            this._holdDownTimeout = null;

            this._pressed = false;
            this.refresh();
            return;
        }

        if (this.isUnavailable) {
            return; // Not allowed to press.
        }
        if (!this._pressed) {
            return;
        }

        this.onClick();

        this._pressed = false;
        this.refresh();
    }

    private onClick(): void {
        this._executeSignalAction();

        if (this._usesPageNavigation) {
            this._navigateToPage();
            this._executeLayerActions();
        }
        else {
            // Switch of external page
            this._pageNavigator.setActiveExternalPage(this._destinationSystemPage);
        }
    }
    private _executeLayerActions(): void {
        if (this._layerActions != null) {
            // Layer changes on the target page.
            for (const layerAction of this._layerActions) {
                const destinationPage = this._pageNavigator.getActivePage();
                if (destinationPage != null) {
                    const layer = destinationPage.getLayer(layerAction.layerId);
                    this._updateLayerState(layerAction.action, layer);
                }
            }
        }
    }

    private _executeSignalAction(): void {
        if (this._signalAction != null) {
            if (this._signalAction === SignalActionEnum.setMfdSignal) {
                this.communicationHandler.send(Protocol.toggleCommand(this.mfdSignalId, true, false, false));
            }
        }
    }

    private _navigateToPage(): void {
        if (!this._destinationPageId != null && this._destinationPageId.length !== 0) {
            this._pageNavigator.setActivePageById(this._destinationPageId);
        }

    }

    private _updateLayerState(action: LayerActionEnum, layer: Layer): void {
        if (action === LayerActionEnum.doNothing) {
            return;
        }

        if (action === LayerActionEnum.show && layer.visible || action === LayerActionEnum.hide && !layer.visible) {
            return;
        }

        const newVisibilityState = action === LayerActionEnum.toggle ? (!layer.visible) : (!(action === LayerActionEnum.hide));

        layer.visible = newVisibilityState;
    }

    private triggerClickAfterHoldDown(): void {
        if (this._pressed) {
            this.onClick();
            this._pressed = false;
            this.refresh();
        }
    }
}
