import { ICommunicationHandler } from '../../infrastructure/communication/communication-handler';
import { MfdStatusUpdateDataContent, StatusUpdateDataContent } from '../../infrastructure/communication/protocol';

export abstract class CommunicationElement {

    protected communicationHandler: ICommunicationHandler;
    private _mfdSignalId: number;
    private _nmeaSignalId: number;
    public isUnavailable: boolean = false;

    constructor(communicationHandler: ICommunicationHandler, element: HTMLElement) {
        this._mfdSignalId = parseInt(element.dataset.ebpSignalId, 10);
        this._nmeaSignalId = parseInt(element.dataset.nmeaSignalId, 10);
        this.communicationHandler = communicationHandler;
        if (!isNaN(this._mfdSignalId)) {
            this.communicationHandler.addOnMfdValueReceivedDelegate(this._mfdSignalId, (value: MfdStatusUpdateDataContent) => this._onMfdDataReceived(value));
        }
        if (!isNaN(this._nmeaSignalId)) {
            this.communicationHandler.addOnNmeaValueReceivedDelegate(this._nmeaSignalId, (value: StatusUpdateDataContent) => this._onNmeaDataReceived(value));
        }
    }

    // eslint-disable-next-line @typescript-eslint/no-empty-function
    public onMfdDataReceived(_: MfdStatusUpdateDataContent): void { }

    // eslint-disable-next-line @typescript-eslint/no-empty-function
    public onNmeaDataReceived(_: StatusUpdateDataContent): void { }

    private _onNmeaDataReceived(_message: StatusUpdateDataContent): void { this.onNmeaDataReceived(_message) }

    private _onMfdDataReceived(message: MfdStatusUpdateDataContent): void {
        if (this.allowSignalTypes().length > 0 && !this.allowSignalTypes().includes(message.type)) {
            return;
        }

        if (this.isUnavailable !== message.unavailable) {
            this.isUnavailable = message.unavailable;
            this.onUnavailableChanged();
            this.evaluateVisibility();
        }

        this.onMfdDataReceived(message);
    }

    public abstract onUnavailableChanged(): void;

    public abstract allowSignalTypes(): number[];

    public abstract evaluateVisibility(): void;

    get mfdSignalId(): number {
        return this._mfdSignalId;
    }
    get nmeaSignalId(): number {
        return this._nmeaSignalId;
    }
}
