import { FileLoadHelper } from './file-load-helper';
import { ICommunicationHandler, Command } from './communication/communication-handler';

export interface ISettingsReader {
    readonly isLoaded: boolean;
    readonly settings: SettingRow[];
    load(): Promise<void>;
    getSetting(key: SettingKey): string;
    addOnSettingsReloadedDelegate(delegate: OnSettingsReloadedDelegate): void;

}

export type OnSettingsReloadedDelegate = () => void;
export class SettingsReader implements ISettingsReader {
    private _settings: SettingRow[] = [];
    private _communicationHandler: ICommunicationHandler = null;
    private _onSettingsChangedDelegate: OnSettingsReloadedDelegate[] = [];

    constructor(communicationHandler: ICommunicationHandler) {
        this._communicationHandler = communicationHandler;
        this._communicationHandler.addOnCommandReceivedDelegate((command: Command) => this._onCommandReceived(command));
    }

    public async load(): Promise<void> {
        const data = await FileLoadHelper.getJsonFile('wduSettings.json');
        this._settings.splice(0, this._settings.length);
        for (const setting of data.Settings) {
            if (setting.isExternalSetting == null) {
                setting.isExternalSetting = false;
            }
            if (setting.isOverridden == null) {
                setting.isOverridden = false;
            }
            this.settings.push(setting as SettingRow);
        }
        for (const delegate of this._onSettingsChangedDelegate) {
            delegate();
        }
    }

    public get isLoaded(): boolean {
        return this._settings.length > 0;
    }

    public get settings(): SettingRow[] {
        return this._settings;
    }
    public getSetting(key: SettingKey): string {
        return this._settings.find((s: any) => s.title === key).currentSetting.symbol;
    }

    public addOnSettingsReloadedDelegate(delegate: OnSettingsReloadedDelegate): void {
        this._onSettingsChangedDelegate.push(delegate);
    }

    private _onCommandReceived(command: Command): void {
        switch (command) {
            case Command.settingsUpdated:
                void this.load();
                break;

        }
    }
}

export class SettingRow {
    public title: string = '';
    public description: string = '';
    public currentSetting: SettingData = null;
    public possibleChoices: SettingChoiceData[] = [];
}

export class SettingData {
    public symbol: string = '';
    public description: string = '';
    public isOverridden: boolean = false;
    public isExternalSetting: boolean = true;

}

export class SettingChoiceData {
    public symbol: string = '';
    public description: string = '';
}

// eslint-disable-next-line no-shadow
export enum SettingKey {
    language = 'Language',
    depth = 'Depth',
    distanceUnit = 'DistanceUnit',
    fuelEconomyUnit = 'FuelEconomyUnit',
    height = 'Height',
    pressure = 'Pressure',
    atmosphericPressure = 'AtmosphericPressure',
    speed = 'Speed',
    temperature = 'Temperature',
    volume = 'Volume',
    volumeRate = 'VolumeRate',
    screensaver = 'ScreenSaver'

}
