import { Gauge } from './components/gauge';
import { Button } from './components/button';
import { LevelGauge } from './components/level-gauge';
import { ImageComponent } from './components/image';
import { NavigationButton } from './components/navigation-button';
import { SignalValue } from './components/signal-value';
import { TextBox } from './components/textbox';
import { Slider } from './components/slider';
import { Indicator } from './components/indicator';
import { Page } from './page';
import { CommunicationHandler } from '../../infrastructure/communication/communication-handler';
import { ColorRectangle } from './components/color-rectangle';
import { ValueConverter } from '../../infrastructure/value-converter/value-converter';
import { PageNavigator } from './page-navigator';
import { Layer } from './layer';
import { Component } from './components/component';
import { ViewPort } from './viewPort';
import { ScreenSaverButton } from './components/screen-saver-button';
import { ScreenSaver } from './screen-saver';
import { Log } from '../../infrastructure/log';
import { MagnitudeConverter } from '../../infrastructure/magnitude-converter';
import { NumberHelpers } from './helpers';
import { GaugeSlicedOverlay } from './components/gauge-sliced-overlay';

export class ComponentFactory {
    private _communicationHandler: CommunicationHandler = null;
    private _valueConverter: ValueConverter = null;
    private _pageNavigator: PageNavigator = null;
    private _screenSaver: ScreenSaver = null;
    private _magnitudeConverter: MagnitudeConverter = null;

    constructor(valueConverter: ValueConverter, pageNavigator: PageNavigator, communicationHandler: CommunicationHandler, screenSaver: ScreenSaver, magnitudeConverter: MagnitudeConverter) {
        this._valueConverter = valueConverter;
        this._pageNavigator = pageNavigator;
        this._communicationHandler = communicationHandler;
        this._screenSaver = screenSaver;
        this._magnitudeConverter = magnitudeConverter;
    }

    public create(): [Page[], ViewPort[]] {
        const pages: Page[] = [];
        const viewPorts: ViewPort[] = [];

        const viewPortElements: HTMLElement[] = Array.from(document.querySelectorAll('.page-collection'));
        for (const viewPortElement of viewPortElements) {
            const viewPortId = viewPortElement.dataset.pageCollectionId;
            const screenSaverPageId = viewPortElement.dataset.screenSaverPageId;
            const aspectRatio = parseFloat(viewPortElement.dataset.aspectRatio);
            const pageElements: HTMLElement[] = Array.from(viewPortElement.querySelectorAll('.page'));
            const viewPort = new ViewPort(viewPortElement, viewPortId, aspectRatio, screenSaverPageId);
            viewPorts.push(viewPort);
            let isStartPage: boolean = true;
            for (const pageElement of pageElements) {
                const pageId = pageElement.id;
                const page = new Page(pageElement, pageId, viewPortId, isStartPage);
                isStartPage = false;
                pages.push(page);
                const layers = this._createLayers(pageElement, page);
                page.initialize(layers);
            }
        }
        return [pages, viewPorts];
    }
    private _createComponents(element: Element): Component[] {
        const components: Component[] = [];
        element.querySelectorAll('.component').forEach((componentElement: HTMLElement) => {
            const component = this.createComponent(componentElement);
            if (component != null) {
                component.initialize();
                components.push(component);
            }
        });

        return components;
    }

    private _createLayers(el: HTMLElement, page: Page): Layer[] {
        const layers: Layer[] = [];

        el.querySelectorAll('.layer').forEach((componentElement: HTMLElement) => {
            const layer = this.createLayer(componentElement, page);
            const components = this._createComponents(componentElement);
            layer.initialize(components);
            layers.push(layer);
        })

        return layers;
    }

    private createLayer(layerElement: HTMLElement, page: Page): Layer {
        return new Layer(layerElement, this._communicationHandler, page);
    }

    private createComponent(componentElement: HTMLElement): Component {
        if (componentElement.classList.contains('gauge')) {
            if (NumberHelpers.parseBooleanArgument(componentElement.dataset.useSlicedOverlay)) {
                return new GaugeSlicedOverlay(componentElement, this._communicationHandler);
            } else {
                return new Gauge(componentElement, this._communicationHandler);
            }
        } else if (componentElement.classList.contains('button')) {
            return new Button(componentElement, this._communicationHandler);
        } else if (componentElement.classList.contains('levelgauge')) {
            return new LevelGauge(componentElement, this._communicationHandler);
        } else if (componentElement.classList.contains('image')) {
            return new ImageComponent(componentElement, this._communicationHandler);
        } else if (componentElement.classList.contains('navigationbutton')) {
            return new NavigationButton(componentElement, this._communicationHandler, this._pageNavigator);
        } else if (componentElement.classList.contains('signalvalue')) {
            return new SignalValue(componentElement, this._communicationHandler, this._valueConverter, this._magnitudeConverter);
        } else if (componentElement.classList.contains('textbox')) {
            return new TextBox(componentElement, this._communicationHandler);
        } else if (componentElement.classList.contains('slider')) {
            return new Slider(componentElement, this._communicationHandler);
        } else if (componentElement.classList.contains('indicator')) {
            return new Indicator(componentElement, this._communicationHandler);
        } else if (componentElement.classList.contains('colorrectangle')) {
            return new ColorRectangle(componentElement, this._communicationHandler);
        } else if (componentElement.classList.contains('screensaver-button')) {
            return new ScreenSaverButton(componentElement, this._communicationHandler, this._screenSaver);
        } else {
            Log.logError('Unknown component found.');
            return null;
        }
    }
}
