        valueSubTypes: [
            {
                valueSubType: ValueSubType.TIME_DURATION_STRING,
                unitLabel: '',
                wduSettingsId: '',
                conversion: (value) => {
                    // 24 h setting
                    const dayInSeconds = 86400;
                    const hourInSeconds = 3600;
                    const minuteInSeconds = 60;

                    const days = Math.floor(value / dayInSeconds);
                    const hours = Math.floor((value % dayInSeconds) / hourInSeconds);
                    const minutes = Math.floor((value % hourInSeconds) / minuteInSeconds);
                    const seconds = Math.floor(value % minuteInSeconds);

                    let str = '';
                    str += (days > 0) ? days + 'd' : '';
                    str += (days > 0 || hours > 0) ? hours + 'h' : '';
                    str += (days > 0 || hours > 0 || minutes > 0) ? minutes + 'm' : '';
                    str += seconds + 's';
                    return str;
                }
            }
        ]
