import { WebSocketManager, SocketState } from '../../infrastructure/communication/web-socket-manager';
import { Log } from '../../infrastructure/log';

export class DataSubscription {
    private _webSocketManager: WebSocketManager;
    constructor(webSocketManager: WebSocketManager) {
        this._webSocketManager = webSocketManager;
    }

    public start(): void {
        this._webSocketManager.addOnSocketStateChangedDelegate((state: SocketState) => {
            if (state === SocketState.open) {
                Log.logInfo('Web socket state changed to open. About to start a new subscription');
                this._sendSubscriptionRequests();
                this._sendN2kSubscriptionRequestsToWdu();
            }
        });
    }

    private _sendSubscriptionRequests(): void {
        const element = document.getElementById('mfd-channel-subscription');
        if (element != null) {
            this._webSocketManager.send(element.innerHTML);
        }
    }

    private _sendN2kSubscriptionRequestsToWdu(): void {
        const element = document.getElementById('n2k-dataItem-subscription');
        if (element != null) {
            this._webSocketManager.send(document.getElementById('n2k-dataItem-subscription').innerHTML);
        }
    }
}
