import { CommunicationHandler } from './communication/communication-handler';
import { FileLoadHelper } from './file-load-helper';

export class NetlistChecksumHandler {
    private _configurationChecksumFromProject: number = null;
    private _communicationHandler: CommunicationHandler;

    public get configurationChecksumFromWDU(): number {
        return this._communicationHandler.configurationChecksumFromWDU;
    }

    public get configurationChecksumFromProject(): number {
        return this._configurationChecksumFromProject;
    }

    constructor(communicationHandler: CommunicationHandler) {
        this._communicationHandler = communicationHandler;
    }

    public async initialize(): Promise<void> {
        await this._getProjectInfo();
    }

    public hasBothChecksums(): boolean {
        return this.configurationChecksumFromWDU != null && this._configurationChecksumFromProject != null;
    }

    public isChecksumValid(): boolean {
        return this.configurationChecksumFromWDU != null
            && this._configurationChecksumFromProject != null
            && this._configurationChecksumFromProject === this.configurationChecksumFromWDU;
    }

    private async _getProjectInfo(): Promise<void> {
        try {
            const file = await FileLoadHelper.getJsonFile('projectinfo.json');
            // tslint:disable-next-line:no-string-literal
            const checkSum = file['netlistChecksum'];
            this._configurationChecksumFromProject = checkSum == null ? 0 : parseInt(checkSum, 10);
        } catch {
            // nop
        }
    }
}
