import { SettingsReader, SettingKey } from '../../infrastructure/settings-reader';
import { Timer } from '../../infrastructure/timer';
import { PageNavigator } from './page-navigator';
export type OnStatusChangeDelegates = () => void;
export class ScreenSaver {

    private _settingsReader: SettingsReader;
    private _pageNavigator: PageNavigator;
    private _screenSaverIdle: number = 0; // milliseconds
    private _lastInputTime: number;
    private _screensaverIsActive: boolean = false;
    private _intervalTime: number = 10000; // milliseconds
    private _currentInterval: NodeJS.Timeout;
    private _ignoreEvents: string[] = ['mousedown', 'mousemove', 'mouseout', 'mouseover', 'mouseup', 'onwheel', 'touchstart', 'touchcancel', 'touchmove'];
    private _closeEvents: string[] = ['touchend', 'click'];
    private _isEnabled: boolean = false;
    private _onStatusChangeDelegates: OnStatusChangeDelegates[] = [];
    private _screenSaverPageId: string = '';
    private _screenSaverPageExists: boolean = false;
    private _screenSaverActivatedTime: number = 0;

    public constructor(settingsReader: SettingsReader, pageNavigator: PageNavigator) {
        this._settingsReader = settingsReader;
        this._pageNavigator = pageNavigator;
        this._lastInputTime = Timer.timestamp();

    }

    public initialize(): void {
        this._settingsReader.addOnSettingsReloadedDelegate(() => this._refresh());
        this._refresh();
        this._registerEvents();
    }

    public reset(): void {
        this._screensaverIsActive = false;
        this._lastInputTime = Timer.timestamp();
        void this._refresh();
    }

    public onScreensaverEnabled(delegate: OnStatusChangeDelegates): void {
        this._onStatusChangeDelegates.push(delegate);
    }

    public get isEnabled(): boolean {
        return this._isEnabled;
    }
    public activate(): void {
        if (this.isEnabled) {
            this._lastInputTime = -99999;
            this._screensaverInterval();
        }
    }
    private _registerEvents(): void {
        // eslint-disable-next-line @typescript-eslint/no-this-alias
        const self = this;
        this._closeEvents.forEach(theEvent => {
            window.addEventListener(theEvent, (event): void => { self._onValidCloseEvent(event); }, true);
        });
        this._ignoreEvents.forEach(theEvent => {
            document.addEventListener(theEvent, (event): void => { self._onIgnoreEvent(event); }, true);
        });
    }

    private _refresh(): void {
        this._ensuerScreenSaverPageExists();
        this._ensureEnabled();

        clearTimeout(this._currentInterval);

        if (!this.isEnabled && this._screensaverIsActive) {
            this._pageNavigator.navigateBack();
            this._screensaverIsActive = false;
        } else {
            // TODO: Use arrow function instead?
            // eslint-disable-next-line @typescript-eslint/no-implied-eval
            this._currentInterval = setInterval(this._screensaverInterval.bind(this), this._intervalTime);
        }
    }
    private _ensuerScreenSaverPageExists(): void {
        this._screenSaverPageId = this._pageNavigator.getActiveViewPort() != null ? this._pageNavigator.getActiveViewPort().screenSaverPageId : '';
        this._screenSaverPageExists = this._screenSaverPageId !== '' ? true : false;
    }
    private _ensureEnabled(): void {
        const thePrevStatus = this._isEnabled;

        try {
            this._screenSaverIdle = parseInt(this._settingsReader.getSetting(SettingKey.screensaver), 10) * 60; // seconds
        }
        catch (e) {
            // setting is 0;
            this._isEnabled = false;
        }
        this._isEnabled = this._screenSaverIdle === 0 ? false : true;

        this._isEnabled = this._isEnabled === true && this._screenSaverPageExists;
        if (thePrevStatus !== this._isEnabled) {
            for (const delegate of this._onStatusChangeDelegates) {
                delegate();
            }
        }
    }

    private _screensaverInterval(): void {
        const timeSinceLastInput =  Timer.elapsedSeconds(this._lastInputTime);
        if (!this._screensaverIsActive && this._isEnabled && (timeSinceLastInput - this._screenSaverIdle) > 0) {
            this._screensaverIsActive = true;
            this._screenSaverActivatedTime = Timer.timestamp();
            this._pageNavigator.setActivePageById(this._screenSaverPageId);
        }
    }
    private _onIgnoreEvent(event: Event): void {
        if (this._screensaverIsActive) {
            event.stopPropagation();
        }
    }
    private _onValidCloseEvent(event: Event): void {
        this._lastInputTime = Timer.timestamp();
        if (this._screensaverIsActive && Timer.elapsedSeconds(this._screenSaverActivatedTime) >= 1  ) {
            event.stopPropagation();
            event.preventDefault();
            // Navigate back
            this._pageNavigator.navigateBack();
            this._screensaverIsActive = false;
        }
    }
}
