import { Log } from './log';

/* eslint-disable no-console */
export class FileLoadHelper {

    public static xhrRequest(xhr: XMLHttpRequest, method: string, url: string): Promise<any> {
        return new Promise((resolve, reject) => {
            xhr.open(method, url, true);
            xhr.onload = () => {
                if (xhr.status >= 200 && xhr.status < 400) {
                    resolve(xhr.response);
                } else {
                    Log.logError(`Error response from xhrRequest, url: ${url}`, xhr.statusText);
                    reject({
                        status: xhr.status,
                        statusText: xhr.statusText
                    });
                }
            };
            xhr.onerror = (ev: ProgressEvent<EventTarget>) => {
                Log.logError(xhr, ev);
                reject({
                    status: xhr.status,
                    statusText: xhr.statusText
                });
            };
            xhr.send();
        });
    }

    public static getJsonFile(fileName: string): Promise<any> {
        const xhr = new XMLHttpRequest();
        xhr.overrideMimeType('application/json');
        xhr.responseType = 'json';
        const promise = (resolve: (value: any) => void, reject: (reason?: any) => void) => {
            xhr.open('GET', fileName, true);
            xhr.onload = () => {
                if (xhr.status >= 200 && xhr.status < 400) {
                    resolve(xhr.response);
                } else {
                    Log.logError(`Error loading file ${fileName}`, xhr.statusText);
                    reject({
                        status: xhr.status,
                        statusText: xhr.statusText
                    });
                }
            };
            xhr.onerror = (ev: ProgressEvent<EventTarget>) => {
                Log.logError(xhr, ev);
                reject({
                    status: xhr.status,
                    statusText: xhr.statusText
                });
            };
            xhr.send();
        };
        return FileLoadHelper.retryPromise(promise, 10);
    }

    public static async retryPromise<T>(fn: (resolve: (value: T | PromiseLike<T>) => void, reject: (reason?: any) => void) => void, retries: number, interval = 100): Promise<T> {
        try {
            // try to resolve the promise
            const data = await new Promise(fn);
            // if resolved simply return the result back to the caller
            return data;
        } catch (e) {
            // if the promise fails and we are down to 1 try we reject
            if (retries > 0) {
                await new Promise(r => setTimeout(r, interval));
                return FileLoadHelper.retryPromise(fn, retries - 1, interval);
            }
            Log.logError(`Failed to execute promise, retries left ${retries}`);
            return Promise.reject(e);
        }
    }
}
