import { HtmlHelpers } from '../graphics/helpers';

export class AppSelectorLinkTimer {
    private _globalMouseUpEvent: (event: MouseEvent) => boolean;
    private _globalMouseMoveEvent: (event: MouseEvent) => boolean;
    private _globalTouchEndEvent: (event: TouchEvent) => boolean;
    private _globalTouchMoveEvent: (event: TouchEvent) => boolean;
    private _timeout: any;
    private _onExecute: () => void;
    public spinner: HTMLDivElement;
    public progressBarWrapper: HTMLDivElement;
    public progressBar: HTMLDivElement;
    public active: boolean = false;
    public startPosX: number;
    public startPosY: number;

    constructor(onExecute: () => void) {
        this.spinner = document.querySelector('.app-selector-link-timer');
        HtmlHelpers.hideElement(this.spinner);
        this._onExecute = onExecute;
        document.addEventListener('mousedown', (event) => {
            if (this.active) {
                clearTimeout(this._timeout);
            }
            document.addEventListener('mouseup', this._globalMouseUpEvent);
            document.addEventListener('mousemove', this._globalMouseMoveEvent);
            this.activate(event.clientX, event.clientY);
            return false;
        });

        document.addEventListener('touchstart', (event) => {
            if (this.active) {
                clearTimeout(this._timeout);
            }
            document.addEventListener('touchend', this._globalTouchEndEvent);
            document.addEventListener('touchcancel', this._globalTouchEndEvent);
            document.addEventListener('touchmove', this._globalTouchMoveEvent, { passive: true });

            const evt = (event as any).originalEvent === undefined ? event : (event as any).originalEvent;
            this.activate(evt.touches[0].clientX, evt.touches[0].clientY);
            return false;
        }, { passive: true });

        this._globalMouseUpEvent = (_event) => {
            this.deactivate();

            document.removeEventListener('mouseup', this._globalMouseUpEvent);
            document.removeEventListener('mousemove', this._globalMouseMoveEvent);
            return false;
        };

        this._globalMouseMoveEvent = (event) => {
            if (!this.isMoveDistanceTooLong(event.clientX, event.clientY)) {
                return;
            }

            this.deactivate();

            document.removeEventListener('mouseup', this._globalMouseUpEvent);
            document.removeEventListener('mousemove', this._globalMouseMoveEvent);

            return false;
        };

        this._globalTouchEndEvent = (_event) => {
            this.deactivate();

            document.removeEventListener('touchend', this._globalTouchEndEvent);
            document.removeEventListener('touchcancel', this._globalTouchEndEvent);
            document.removeEventListener('touchmove', this._globalTouchMoveEvent);

            return false;
        };

        this._globalTouchMoveEvent = (event: any) => {
            const evt = event.originalEvent === undefined ? event : event.originalEvent;
            if (!this.isMoveDistanceTooLong(evt.touches[0].clientX, evt.touches[0].clientY)) {
                return false;
            }
            this.deactivate();

            document.removeEventListener('touchend', this._globalTouchEndEvent);
            document.removeEventListener('touchcancel', this._globalTouchEndEvent);
            document.removeEventListener('touchmove', this._globalTouchMoveEvent);

            return false;
        };
    }

    private activate(posX: number, posY: number): void {
        // If used on a zoomed area (i.e., media-query for appselector screen), adjust the position accordingly.
        const zoom = parseInt(window.getComputedStyle(document.body).getPropertyValue('zoom'));
        if(zoom != null && !isNaN(zoom)) {
            posX /= zoom;
            posY /= zoom;
        }

        this.active = true;
        this.spinner.style.left = posX + 'px';
        this.spinner.style.top = posY + 'px';
        this.startPosX = posX;
        this.startPosY = posY;
        HtmlHelpers.showElement(this.spinner);

        this._timeout = setTimeout(() => {
            this._onExecute();
            HtmlHelpers.hideElement(this.spinner);
        }, 10000);
    }

    private deactivate() : void {
        this.active = false;
        clearTimeout(this._timeout);
        HtmlHelpers.hideElement(this.spinner);
    }

    private isMoveDistanceTooLong(posX: number, posY: number): boolean {
        const distance = Math.sqrt(Math.pow(this.startPosX - posX, 2) + Math.pow(this.startPosY - posY, 2));
        const maxDistance = 100;
        return distance > maxDistance;
    }
}