import { Page } from './page';
import { ExternalPageNavigator } from './external-page-navigator';
import { ViewPort } from './viewPort';
import { UrlHelper } from '../../infrastructure/url-helper';

export class PageNavigator {

    public static createUrlWithReturnParameter(navigationUrl: string, pageId: string = null): string {
        return UrlHelper.createUrlWithReturnParameter(navigationUrl,pageId);
    }

    private _maxHistoryLength: number = 10;
    private _pages: Page[] = [];
    private _currentViewPort: ViewPort = null;
    private _externalPageNavigator: ExternalPageNavigator = new ExternalPageNavigator();
    private _history: NavigationHistoryItem[] = [];
    private _activePageId: string = '';

    public setPages(pages: Page[]): void {
        this._pages = pages;
        if (this._pages.length > 0) {
            this._setActivePage(this._pages[0], true);
        }
    }

    public setActivePageByViewPort(viewPort: ViewPort): void {
        this.hideAllPages();
        this._currentViewPort = viewPort;
        this._history.length = 0; // Clear the array

        const urlPageId = UrlHelper.getUrlParamValue('pageId');
        UrlHelper.removeUrlParameter('pageId')

        let page = this._pages.find(p => p.viewPortId === this._currentViewPort.id && p.pageId === urlPageId);
        if (page != null) {
            this._setActivePage(page, true);
        }
        else {
            page = this._pages.find(p => p.viewPortId === viewPort.id && p.isStartPage);
            this._setActivePage(page, true);
        }
    }
    public setActivePageById(pageId: string): void {
        let page = this._pages.find(p => p.pageId === pageId && p.viewPortId === this._currentViewPort.id);
        if (page == null) { // if return url not in view port e.g
            page = this._pages.find(p => p.viewPortId === this._currentViewPort.id && p.isStartPage);
        }
        this._setActivePage(page, true);
    }
    public setActiveExternalPage(targetUrl: string): void {
        const navHistory = new NavigationHistoryItem();
        navHistory.externalPageUrl = targetUrl;
        this._history.splice(0, 0, navHistory);
        const url = PageNavigator.createUrlWithReturnParameter(targetUrl, this._activePageId);
        window.location.href = url;

    }

    public navigateBack(): void {
        if (this._history.length < 2) {
            return;
        }
        const target = this._history[1];
        this._history.shift(); // remove first element (current page)

        if (target.isExternalPage) {
            this._setActiveExternalPage(target.externalPageUrl, false);
        } else if (target.page != null) {
            this._setActivePage(target.page, false);
        }
    }

    public getActivePage(): Page {
        const currentPage = this._history.length > 0 ? this._history[0].page : null;
        return this._pages.find(p => p === currentPage);
    }
    public getActiveViewPort(): ViewPort {
        return this._currentViewPort;
    }
    private hideAllPages(): void {
        for (const page of this._pages) {
            page.setVisibility(false);
        }
    }

    private _setActivePage(page: Page, addToHistory: boolean): void {
        this._externalPageNavigator.hide();
        if (addToHistory) {
            const navHistoryItem = new NavigationHistoryItem();
            navHistoryItem.page = page;
            this._history.splice(0, 0, navHistoryItem);
            while (this._history.length >= this._maxHistoryLength) {
                this._history.pop();
            }
        }
        for (const p of this._pages) {
            const activePage = page === p;
            p.setVisibility(activePage);
            if (activePage) {
                p.redraw();
                this._activePageId = p.pageId;
            }
        }
    }

    private _setActiveExternalPage(localUrl: string, addToHistory: boolean): void {
        if (addToHistory) {
            const navHistory = new NavigationHistoryItem();
            navHistory.externalPageUrl = localUrl;
            this._history.splice(0, 0, navHistory);
        }
        this.hideAllPages();
        this._externalPageNavigator.show(localUrl, () => this.navigateBack());
    }


}

export class NavigationHistoryItem {

    public page: Page = null;
    public externalPageUrl: string = null;
    public get isExternalPage(): boolean {
        return this.externalPageUrl != null;
    }

}
