import { ApplicationSettings, ApplicationType } from './application-settings';
import { SettingsReader, SettingKey } from '../../infrastructure/settings-reader';
import { FileLoadHelper } from '../../infrastructure/file-load-helper';

export class LanguageHandler {
    private _languageResources: LanguageResource[];
    private _activeLanguage: string = 'default';
    private _applicationSettings: ApplicationSettings = null;
    private _settingsReader: SettingsReader;
    constructor(wduSettingHandler: SettingsReader, applicationSettings: ApplicationSettings) {
        this._settingsReader = wduSettingHandler;
        this._applicationSettings = applicationSettings;
    }
    public async initialize(): Promise<void> {
        await this._parseLanguageData();
        this._settingsReader.addOnSettingsReloadedDelegate(() => this._propagateSettings());
        if (this._settingsReader.isLoaded) {
            this._propagateSettings();
        }

    }
    private _setActiveLanguage(language: string): void {
        this._activeLanguage = language;
        if (!this._languageResources) {
            return;
        }
        for (const resource of this._languageResources) {
            const element = document.getElementById(resource.elementId);
            if (element.tagName.toLocaleLowerCase() === 'img') {
                const imgEl = element as HTMLImageElement;
                imgEl.src = this._getValue(this._activeLanguage, resource.resources);
            } else {
                element.innerText = this._getValue(this._activeLanguage, resource.resources);
            }
        }
    }

    private async _parseLanguageData(): Promise<void> {
        try {
            const fileName = this._getResourceFileName();
            this._languageResources = await FileLoadHelper.getJsonFile(fileName);
        } catch {
            //
        }
    }
    private _getResourceFileName(): string {
        if (this._applicationSettings.type === ApplicationType.normal) {
            return this._applicationSettings.index === 0 ? 'index-localization.json' : `index-localization${this._applicationSettings.index}.json`;
        } else {
            return this._applicationSettings.index === 0 ? 'overlaybar-localization.json' : `overlaybar-localization${this._applicationSettings.index}.json`;
        }
    }
    private _getValue(languageKey: string, resources: LanguageData[]): string {
        let data = resources.find((l) => l.languageKey === languageKey);
        if (data == null) {
            data = resources.find((l) => l.languageKey === 'default');
        }
        return data.value;
    }

    // private _load(): void {
    //     const request = new XMLHttpRequest();
    //     request.overrideMimeType('application/json');
    //     request.responseType = 'json';
    //     const fileName = this._getResourceFileName();
    //     request.open('GET', fileName, true);

    //     request.onload = () => {
    //         if (request.status >= 200 && request.status < 400) {
    //             // Success!
    //             const data = request.response;
    //             this._languageResources = data;
    //         } else {
    //             // TODO: error
    //         }
    //     };

    //     request.onerror = () => {
    //         // TODO: error
    //     };

    //     request.send();
    // }

    private _propagateSettings(): void {
        this._setActiveLanguage(this._settingsReader.getSetting(SettingKey.language));
    }
}

class LanguageResource {
    public elementId: string = null;
    public resources: LanguageData[] = [];
}

class LanguageData {
    public languageKey: string;
    public value: string;
}
