import { Layer } from './layer';
import { HtmlHelpers } from './helpers';

export class Page {
    private _pageId: string;
    private _isStartPage: boolean;
    private _viewPortId: string;
    private _element: HTMLElement;
    private _visible: boolean = false;
    private _layers: Layer[] = null;

    constructor(pageElement: HTMLElement, pageId: string, viewPortId: string, isStartPage: boolean) {
        this._element = pageElement;
        this._pageId = pageId;
        this._viewPortId = viewPortId;
        this._isStartPage = isStartPage;
    }

    get isStartPage(): boolean {
        return this._isStartPage;
    }

    get viewPortId(): string {
        return this._viewPortId;
    }
    get isVisible(): boolean {
        return this._visible;
    }
    get pageId(): string {
        return this._pageId;
    }

    public initialize(layers: Layer[]): void {
        this._layers = layers;
    }

    public getLayers(): Layer[] {
        return this._layers;
    }

    public getLayer(layerId: string): Layer {
        return this._layers.find(l => l.id === layerId);
    }

    public redraw(): void {
        for (const layer of this._layers) {
            layer.redraw();
        }
    }

    public setVisibility(visible: boolean): void {
        HtmlHelpers.setElementVisibility(this._element, visible);
        if (this._visible !== visible) {   // If we're going from visible to not visible: make sure to release all buttons.
            this._visible = visible;
            for (const layer of this._layers) {
                layer.onParentVisibilityChanged();
            }
        }
    }

}
