import { PageNavigator } from './page-navigator';
import { ViewPort } from './viewPort';
import { ScreenSaver } from './screen-saver';
import { Log } from '../../infrastructure/log';
export class ViewPortManager {
    private _pageNavigator: PageNavigator;
    private _activeViewPortId: string = null;
    private _screenSaver: ScreenSaver;
    private _viewPorts: ViewPort[];
    constructor(pageNavigator: PageNavigator, screenSaver: ScreenSaver) {
        this._pageNavigator = pageNavigator;
        this._screenSaver = screenSaver;
        window.addEventListener('resize', () => this._changeViewPortBasedOnWindowSize());
    }
    public setViewPorts(viewPorts: ViewPort[]): void {
        this._viewPorts = viewPorts;
        this._changeViewPortBasedOnWindowSize();

    }
    private _changeViewPortBasedOnWindowSize(): void {

        Log.logInfo(`Windows size change, window.innerwidth: ${window.innerWidth}, windos.innerHeight ${window.innerHeight}`);
        const currentAspectRatio = window.innerWidth / window.innerHeight;
        Log.logInfo(`Current aspectRatio: ${currentAspectRatio}`);
        const sorted = this._viewPorts.sort((a, b) => Math.abs(a.aspectRatio - currentAspectRatio) - Math.abs(b.aspectRatio - currentAspectRatio));
        if (sorted == null || sorted.length === 0) {
            return;
        }

        const selectedVp = sorted[0];
        if (selectedVp.id !== this._activeViewPortId) {
            for (const vp of this._viewPorts) {
                vp.setVisibility(false);
            }
            Log.logInfo(`Selected viewPort Id: ${selectedVp.id}`);
            selectedVp.setVisibility(true);
            this._activeViewPortId = selectedVp.id;
            this._pageNavigator.setActivePageByViewPort(selectedVp);
            this._screenSaver.reset();
        }
    }
}
